export default "/**\n * googlelocator\n */\n\n.demo--googlelocator {\n\n  &.googlelocator {\n    display: flex;\n    flex-wrap: wrap;\n    @media @max-sm {\n      flex-flow: column;\n    }\n    .flex-auto();\n    @media @min-sm {\n      min-height: 100vh;\n    }\n  }\n\n  // .googlelocator_main\n\n  .googlelocator_main {\n    position: relative;\n    .flex-auto();\n    min-height: 100%;\n    @media @min-sm {\n      position: fixed;\n      top: 0;\n      right: 0;\n      width: 100%;\n      height: 100%;\n      padding-left: 35rem;\n    }\n    @media @min-md {\n      padding-left: 40rem;\n    }\n    @media @min-lg {\n      padding-left: 45rem;\n    }\n    // hide map on mobile\n    @media @max-sm {\n      .sr-only();\n      height: 100vh;\n      visibility: hidden;\n      pointer-events: none;\n      position: fixed; // @FIX no absolute or bugs search\n      z-index: -1;\n      top: -100vh;\n      left: -100vw;\n      opacity: 0;\n    }\n  }\n\n  .googlelocator_main_inner {\n    position: relative;\n    width: 100%;\n    height: 100%;\n  }\n\n  .googlelocator_main_map {\n    width: 100%;\n    height: 100%;\n  }\n\n  // .googlelocator_aside\n\n  .googlelocator_aside {\n    position: relative;\n    z-index: 1;\n    @media @min-sm {\n      width: 35rem;\n      min-height: 100%;\n      box-shadow: 0 0 1rem rgba(0, 0, 0, 0.2);\n    }\n    @media @min-md {\n      width: 40rem;\n    }\n    @media @min-lg {\n      width: 45rem;\n    }\n  }\n\n  .googlelocator_aside_head {\n    position: relative;\n    .btn-locate-store-outer {\n      position: relative;\n      width: auto;\n    }\n    padding: 3rem;\n    border-bottom: 1px solid @shade-200;\n  }\n\n  .googlelocator_aside_body {\n    position: relative;\n    padding: 3rem;\n\n    // result\n\n    .googlelocator_result {\n      display: none;\n      padding-bottom: 1rem;\n      border-bottom: 1px solid @shade-200;\n      font-size: 1.3rem;\n    }\n\n    [data-xt-googlelocator-result-initial] {\n      display: block;\n    }\n\n    &.found {\n      .googlelocator_result {\n        display: none;\n      }\n      [data-xt-googlelocator-result-founds] {\n        display: block;\n      }\n    }\n\n    &.noplace {\n      .googlelocator_result {\n        display: none;\n      }\n      [data-xt-googlelocator-result-noplace] {\n        display: block;\n      }\n    }\n\n    &.empty {\n      .googlelocator_result {\n        display: none;\n      }\n      [data-xt-googlelocator-result-empty] {\n        display: block;\n      }\n    }\n\n    &.error {\n      .googlelocator_result {\n        display: none;\n      }\n      [data-xt-googlelocator-result-error] {\n        display: block;\n      }\n    }\n\n  }\n\n  // special\n\n  .googlelocator_repeat {\n    position: absolute;\n    z-index: 2;\n    top: 2rem;\n    left: 2rem;\n  }\n\n  // .googlelocator_items\n\n  .googlelocator_items {\n    .margin(top, medium);\n  }\n\n  .googlelocator_item {\n    .padding(y, small);\n    > .list {\n      .list-space(small);\n      flex-wrap: nowrap;\n      align-items: center;\n      justify-content: space-between;\n    }\n  }\n\n  .googlelocator_items .googlelocator_item {\n    // activation when clicking marker\n    cursor: pointer;\n    border: 1px solid transparent;\n    transition: border @time-medium @ease-out;\n    &.active {\n      border-bottom: 1px solid @accent;\n      transition: border @time-medium @ease-in;\n    }\n  }\n\n  .googlelocator_item_name {\n    .full();\n    .h5();\n  }\n\n  .googlelocator_item_address {\n    .full();\n    font-size: 1.3rem;\n    line-height: 1.4;\n    text-transform: uppercase;\n  }\n\n  .googlelocator_item_additional {\n    .full();\n    font-size: 1.3rem;\n    line-height: 1.4;\n    text-transform: uppercase;\n  }\n\n  .googlelocator_item_directions {\n    .link-none();\n    font-size: 1.3rem;\n    line-height: 1.4;\n    text-transform: uppercase;\n  }\n\n  // infoWindow\n\n  .gm-style {\n    // reset\n    .gm-style-iw-d {\n      overflow: visible !important;\n      max-width: none !important;\n      max-height: none !important;\n    }\n    // arrow\n    .gm-style-iw-t {\n      &:after {\n        box-shadow: @shadow-light;\n        background: @white;\n      }\n    }\n    // box\n    .gm-style-iw-c {\n      .padding(all, 2rem) !important;\n      border: 0;\n      border-radius: 0;\n      box-shadow: @shadow-light;\n      background: @white;\n      // scroll\n      overflow-x: hidden !important;\n      overflow-y: auto !important;\n      max-height: 15rem !important;\n      .overflow-style(@mode: inner);\n    }\n    // close\n    .gm-style-iw-d + button {\n      top: 0 !important;\n      right: 0 !important;\n      width: 3rem !important;\n      height: 3rem !important;\n      background: @shade-200-dark !important;\n      border-bottom-left-radius: 1rem;\n      font-size: 25px;\n      color: @black !important;\n      line-height: 0;\n      text-align: center;\n      opacity: 1 !important;\n      &:before {\n        content: '';\n        position: relative;\n        top: -2px;\n      }\n      img {\n        display: none !important;\n      }\n    }\n  }\n\n}\n"