export default "/**\n * overlay-position\n */\n\n.overlay-position {\n  .overlay-container {\n    position: relative;\n    z-index: @index-overlay;\n    .padding(all, none) !important;\n    .overlay-inner {\n      position: static;\n    }\n  }\n  .overlay-inner {\n    .flex-auto();\n    display: flex;\n    flex-direction: column;\n  }\n  .overlay-position-left& {\n    .overlay-container {\n      margin-left: 0;\n      margin-right: auto;\n    }\n  }\n  .overlay-position-right& {\n    .overlay-container {\n      margin-left: auto;\n      margin-right: 0;\n    }\n  }\n}\n"