export default "/**\n * overlay-class-html\n */\n\n.overlay-class-html { // @NOTICE DO NOT APPLY to selector with descendants with position: fixed; or it bugs position: fixed;\n  transition: transform @time-small @ease-out;\n  html.overlay-class-html-left & {\n    transform: translateX(4rem);\n    transition-timing-function: @ease-in;\n  }\n  html.overlay-class-html-right & {\n    transform: translateX(-4rem);\n    transition-timing-function: @ease-in;\n  }\n}\n"