export default "/**\n * card interactive\n */\n\n// variant\n\n.card-default {\n  // interactive\n  a&, label& {\n    &:hover, &.hover {\n      color: @text-dark;\n      > .card-design {\n        border-color: @shade-200;\n        background: @white;\n      }\n    }\n    &:active, &.active {\n      color: @text-dark;\n      > .card-design {\n        border-color: @shade-200;\n        background: @shade-200;\n      }\n    }\n  }\n}\n\n.card-primary {\n  // interactive\n  a&, label& {\n    &:hover, &.hover {\n      color: @text-inverse-dark;\n      > .card-design {\n        border-color: @accent-dark;\n        background: @accent;\n      }\n    }\n    &:active, &.active {\n      color: @text-inverse-dark;\n      > .card-design {\n        border-color: @accent-dark;\n        background: @accent-dark;\n      }\n    }\n  }\n}\n\n// animation\n\n.card {\n  // interactive\n  a&, label& {\n    > .card-design {\n      transition: all @time-small @ease-out;\n    }\n    &:hover, &.hover {\n      > .card-design {\n        transition: all @time-small @ease-in;\n      }\n    }\n    &:active, &.active, input[type=\"checkbox\"]:not(.unstyled):checked ~ &, input[type=\"radio\"]:not(.unstyled):checked ~ & {\n      > .card-design {\n        transition: all @time-small @ease-in;\n      }\n    }\n  }\n}\n"