export default "import { Xt } from 'xtend-library/src/xt.js'\n\nXt.mount.push({\n  matches: '.demo--scroll-container--delay',\n  mount: object => {\n    // init\n\n    let self = new Xt.Scroll(object, {\n      elements: ':scope > *',\n      delayOn: current => {\n        return Math.min((current + 1) * 75, 300)\n      },\n      delayOff: (current, total) => {\n        return Math.min((total - (current + 1)) * 75, 300)\n      },\n      distance: '20%',\n    })\n\n    // unmount\n\n    const unmount = () => {\n      self.destroy()\n      self = null\n    }\n    return unmount\n  },\n})\n"