export default "import { Xt } from 'xtend-library/src/xt.js'\n\nXt.mount.push({\n  matches: '.demo--scroll-container-block',\n  mount: object => {\n    // vars\n\n    const btns = object.querySelectorAll('.demo--scroll-container-block--btn')\n\n    // init\n\n    let self = new Xt.Scroll(object, {\n      elements: ':scope > *',\n    })\n\n    // eventBlock\n\n    const eventBlock = () => {\n      for (const element of self.elements) {\n        // fade out\n        element.dispatchEvent(new CustomEvent('off.trigger.xt'))\n        // block\n        element.classList.add('xt-block')\n        // this doesn't trigger because when .xt-block on and off are blocked\n        element.dispatchEvent(new CustomEvent('on.trigger.xt'))\n      }\n    }\n\n    for (const btn of btns) {\n      btn.addEventListener('click', eventBlock)\n    }\n\n    // unmount\n\n    const unmount = () => {\n      self.destroy()\n      self = null\n    }\n    return unmount\n  },\n})\n"