export default "import { Xt } from 'xtend-library/src/xt.js'\nimport 'xtend-library/src/extensions/scroll/scroll.js'\nimport gsap from 'gsap'\n\nXt.mount.push({\n  matches: '.demo--parallax-title',\n  mount: object => {\n    // init\n\n    let self = new Xt.Scroll(object, {\n      sticky: 'fixed',\n      end: 350,\n    })\n\n    // change\n\n    const eventChange = e => {\n      const element = e.target\n      gsap.set(element, { transformOrigin: 'left center' })\n      gsap.set(element, { opacity: self.detail.ratioInverse, scale: 0.9 + 0.1 * self.detail.ratioInverse })\n    }\n\n    for (const element of self.elements) {\n      element.addEventListener('change.xt', eventChange)\n    }\n\n    // unmount\n\n    const unmount = () => {\n      self.destroy()\n      self = null\n    }\n    return unmount\n  },\n})\n\nXt.mount.push({\n  matches: '.demo--parallax-img',\n  mount: object => {\n    // init\n\n    let self = new Xt.Scroll(object, {\n      sticky: 'fixed',\n      end: 350,\n    })\n\n    // change\n\n    const eventChange = e => {\n      const element = e.target\n      gsap.set(element, { y: 100 * self.detail.ratio, opacity: self.detail.ratioInverse })\n    }\n\n    for (const element of self.elements) {\n      element.addEventListener('change.xt', eventChange)\n    }\n\n    // unmount\n\n    const unmount = () => {\n      self.destroy()\n      self = null\n    }\n    return unmount\n  },\n})\n\nXt.mount.push({\n  matches: '.demo--parallax-footer',\n  mount: object => {\n    // init\n\n    let self = new Xt.Scroll(object, {\n      sticky: true,\n      start: '125%',\n    })\n\n    // change\n\n    const eventChange = e => {\n      const element = e.target\n      gsap.set(element, { opacity: self.detail.ratio, scale: 0.9 + 0.1 * self.detail.ratio })\n    }\n\n    for (const element of self.elements) {\n      element.addEventListener('change.xt', eventChange)\n    }\n\n    // unmount\n\n    const unmount = () => {\n      self.destroy()\n      self = null\n    }\n    return unmount\n  },\n})\n"