export default "/**\n * overlay\n */\n\n.overlay {\n  .toggle-flex();\n  position: fixed;\n  z-index: @index-overlay;\n  top: 0;\n  left: 0;\n  bottom: 0; // @FIX no height or it bugs collapse animation\n  right: 0; // @FIX no width or it bugs collapse animation\n  // scroll\n  overflow: hidden;\n  .overflow-style(@mode: none);\n}\n\n.overlay-container {\n  display: flex;\n  flex-wrap: nowrap;\n  flex-direction: column;\n  justify-content: center;\n  margin: auto; // @FIX http://stackoverflow.com/questions/33454533/cant-scroll-to-top-of-flex-item-that-is-overflowing-container\n  width: 100%;\n  min-height: 100vh;\n}\n\n.overlay-inner {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  align-self: center;\n  position: relative;\n  z-index: @index-backdrop + 1;\n  width: 100%;\n}\n\n.overlay-design {\n  .design-setup();\n  border-radius: inherit;\n}\n\n// special\n\n.overlay-disable {\n  .xt-disable();\n  display: flex !important;\n  overflow: initial !important;\n  html.xt-scrollbar & {\n    overflow: initial !important;\n  }\n  position: static;\n  z-index: initial;\n  top: auto;\n  left: auto;\n  bottom: auto;\n  right: auto;\n  width: auto;\n  height: auto;\n  .overlay-container {\n    z-index: initial;\n    padding: 0;\n    width: 100%;\n    min-height: auto;\n    .overlay-inner {\n      z-index: initial;\n      .overlay-design {\n        display: none;\n      }\n      .card-overlay {\n        .card-disable();\n      }\n      .btn-close {\n        display: none !important;\n      }\n    }\n    .overlay-inner, .card-overlay > .card-inner > .card-content {\n      max-width: none;\n    }\n  }\n  > .backdrop {\n    display: none !important;\n  }\n}\n"