export default "/**\n * separator\n */\n\n.separator {\n  display: inline-block;\n  position: relative;\n  .margin(x, mini);\n  font-size: 1em;\n  vertical-align: baseline;\n}\n\n.separator-dash {\n  &:after {\n    content: '';\n  }\n}\n\n.separator-slash {\n  &:after {\n    content: '/';\n  }\n}\n\n.separator-backslash {\n  &:after {\n    content: '\\\\';\n  }\n}\n\n.separator-pipe {\n  &:after {\n    content: '|';\n  }\n}\n\n.separator-point {\n  &:after {\n    content: '';\n  }\n}\n\n.separator-arrow {\n  &:after {\n    content: '>';\n  }\n}\n"