export default "@import 'xtend-library/src/core/form/_form.less';\n\n/**\n * vars\n */\n\n// select\n\n@select-arrow-size: 4px; // in px\n@select-space-size: 35px; // in px\n\n/**\n * fieldset\n */\n\nfieldset, .fieldset {\n  & + & {\n    .margin(top, medium);\n  }\n}\n\n/**\n * legend\n */\n\nlegend, .legend {\n  .full();\n  .h4();\n  .header-line();\n}\n\n/**\n * form-group\n */\n\n.form-group {\n  position: relative;\n}\n\n.form-group-last {\n  .margin(bottom, none) !important;\n}\n\n// set group variant (for specificity must be before classes used)\n\n.form-group {\n  .form-default & {\n    .form-group.form-group-medium();\n  }\n  .form-inverse & {\n    .form-group.form-group-medium();\n  }\n}\n\n// set group size (for specificity must be before classes used)\n\n.form-small {\n  .form-group {\n    .form-group.form-group-small();\n  }\n  button[type='submit'] {\n    .btn-medium();\n    .btn-wide();\n  }\n}\n\n.form-medium {\n  .form-group {\n    .form-group.form-group-medium();\n  }\n  button[type='submit'] {\n    .btn-large();\n    .btn-wide();\n  }\n}\n\n.form-large {\n  .form-group {\n    .form-group.form-group-large();\n  }\n  button[type='submit'] {\n    .btn-big();\n    .btn-wide();\n  }\n}\n\n// size\n\n.form-group.form-group-small {\n  .margin(bottom, 1.5rem);\n  font-size: 1.2rem;\n}\n\n.form-group.form-group-medium {\n  .margin(bottom, 1.5rem);\n  font-size: 1.4rem;\n}\n\n.form-group.form-group-large {\n  .margin(bottom, 1.5rem);\n  font-size: 1.6rem;\n}\n\n// mode\n\n.form-group-horizontal {\n  display: flex;\n  .row-space-x(medium);\n  align-items: center;\n  text-align: right;\n  .form-label {\n    margin: 0;\n  }\n}\n\n.form-group-horizontal-sm {\n  @media @min-sm {\n    .form-group-horizontal();\n  }\n}\n\n.form-group-inline {\n  display: flex;\n  flex-wrap: wrap;\n  .list-space-x(medium);\n  align-items: baseline;\n  .form-item {\n    width: auto;\n  }\n}\n\n.form-group-inline-sm {\n  @media @min-sm {\n    .form-group-inline();\n  }\n}\n\n/**\n * form-label\n */\n\n.form-label {\n  .margin(bottom, 1em); // size in em inherit .form-group size\n  .font-sans-semibold();\n  .line-height-small();\n  font-size: 0.9em; // size in em inherit .form-group size\n  letter-spacing: .025em;\n  text-transform: uppercase;\n}\n\n// set label variant (for specificity must be before classes used)\n\n.form-label {\n  &.form-label.form-label-default, .form-default & {\n    .form-label-default();\n  }\n  &.form-label.form-label-inverse, .form-inverse & {\n    .form-label-inverse();\n  }\n}\n\n// variant\n\n.form-label-default {\n  .text-default();\n  color: @shade-500;\n}\n\n.form-label-inverse {\n  .text-inverse();\n  color: @text-inverse;\n}\n\n/**\n * form-item\n */\n\n.form-item {\n  padding: .75em 1.2em; // size in em inherit .form-group size\n  .font-sans-regular();\n  .line-height-small();\n  font-size: 1em; // size in em inherit .form-group size\n}\n\n// set item variant (for specificity must be before classes used)\n\n.form-item {\n  &.form-item.form-item-default, .form-default & {\n    .form-item-default();\n  }\n  &.form-item.form-item-inverse, .form-inverse & {\n    .form-item-inverse();\n  }\n}\n\n// variant\n\n.form-item-default {\n  border-radius: 6px;\n  border-width: 1px;\n  border-style: solid;\n  border-color: @shade-200-dark;\n  background-color: @shade-200; // @FIX no background: or it overrides background-image\n  color: @text;\n  &:focus, &.active {\n    border-color: @shade-200-dark;\n    background-color: @shade-200-dark; // @FIX no background: or it overrides background-image\n    color: @text;\n  }\n  // placeholder\n  &:-ms-input-placeholder {\n    color: @shade-500;\n  }\n  &:-moz-placeholder {\n    color: @shade-500;\n  }\n  &::-webkit-input-placeholder {\n    color: @shade-500;\n  }\n  &::-ms-input-placeholder {\n    color: @shade-500;\n  }\n  &::-moz-placeholder {\n    color: @shade-500;\n  }\n  &::placeholder {\n    color: @shade-500;\n  }\n  // validation\n  &.valid {\n    border-color: @success !important;\n  }\n  &.invalid {\n    border-color: @error !important;\n  }\n}\n\n.form-item-inverse {\n  border-radius: 0;\n  border-width: 0;\n  border-style: solid;\n  border-bottom-width: 2px;\n  border-color: transparent;\n  background-color: fade(@shade-900, 25%); // @FIX no background: or it overrides background-image\n  color: @text-inverse-dark;\n  &:focus, &.active {\n    border-color: @white;\n    background-color: fade(@shade-900, 50%); // @FIX no background: or it overrides background-image\n    color: @text-inverse-dark;\n  }\n  // placeholder\n  &:-ms-input-placeholder {\n    color: @text-inverse;\n  }\n  &:-moz-placeholder {\n    color: @text-inverse;\n  }\n  &::-webkit-input-placeholder {\n    color: @text-inverse;\n  }\n  &::-ms-input-placeholder {\n    color: @text-inverse;\n  }\n  &::-moz-placeholder {\n    color: @text-inverse;\n  }\n  &::placeholder {\n    color: @text-inverse;\n  }\n  // validation\n  &.valid {\n    border-color: @success !important;\n  }\n  &.invalid {\n    border-color: @error !important;\n  }\n}\n\n// animation\n\n.form-item {\n  transition: color @time-small @ease-out, border @time-small @ease-out, background-color @time-small @ease-out;\n  &:focus, &.active {\n    transition: color @time-small @ease-in, border @time-small @ease-in, background-color @time-small @ease-in;\n  }\n}\n\n/**\n * textarea\n */\n\n.form-item {\n  textarea& {\n    height: 10rem;\n  }\n}\n\n/**\n * select\n */\n\n.form-item {\n  select:not([multiple])& {\n    &.form-item-default, .form-default & {\n      @select-arrow-color: @text;\n      @select-space-color: fade(@shade-900, 10%);\n      background-image: linear-gradient(45deg, transparent 50%, @select-arrow-color 50%), linear-gradient(135deg, @select-arrow-color 50%, transparent 50%), linear-gradient(to right, @select-space-color, @select-space-color);\n    }\n    &.form-item-inverse, .form-inverse & {\n      @select-arrow-color: @text-inverse;\n      @select-space-color: fade(@shade-900, 10%);\n      background-image: linear-gradient(45deg, transparent 50%, @select-arrow-color 50%), linear-gradient(135deg, @select-arrow-color 50%, transparent 50%), linear-gradient(to right, @select-space-color, @select-space-color);\n    }\n  }\n  select[multiple]& {\n    max-height: 10rem;\n    overflow-x: hidden;\n    overflow-y: auto;\n    .overflow-style(@mode: inner);\n    option {\n      padding: .5rem 0;\n    }\n  }\n}\n\n/**\n * check and radio\n */\n\n// label\n\n.checkradio-label {\n  .margin(all, none); // reset .form-label\n  .font-sans-regular();\n  .line-height-small();\n  font-size: 0.9em; // size in em inherit .form-group size\n  letter-spacing: 0;\n  text-transform: none;\n  // variant\n  &.form-label.form-label-default, .form-default & {\n    color: @text;\n  }\n  &.form-label.form-label-inverse, .form-inverse & {\n    color: @text-inverse;\n  }\n}\n\n// styled\n\n.checkbox, .radio {\n  .form-label();\n  .checkradio-label() !important;\n  padding-left: 26px !important;\n  &:before, &:after {\n    top: 1px;\n    width: 16px;\n    height: 16px;\n  }\n  // variant\n  &.form-label.form-label-default, .form-default & {\n    &:before {\n      background: @shade-200;\n      border: 1px solid @shade-200-dark;\n    }\n    &:after {\n      color: @accent;\n    }\n  }\n  &.form-label.form-label-inverse, .form-inverse & {\n    &:before {\n      background: fade(@shade-900, 25%);\n      border: 1px solid fade(@shade-900, 50%);\n    }\n    &:after {\n      color: @white;\n    }\n  }\n}\n\n.checkbox {\n  &:before {\n    border-radius: 2px;\n  }\n  &:after {\n    .icon-xt();\n    content: @icon-xt-check;\n    font-size: 100%;\n  }\n}\n\n.radio {\n  &:before {\n    border-radius: 50%;\n  }\n  &:after {\n    .icon-xt();\n    content: @icon-xt-circle;\n    font-size: 50%;\n  }\n}\n\n// unstyled\n\n.checkradio-label-unstyled {\n  .checkradio-label() !important;\n  .padding(left, small) !important; // space from checkbox/radio\n  top: -3px !important;\n}\n\n// validation\n\ninput[type=\"checkbox\"], input[type=\"radio\"] {\n  &.valid {\n    & ~ .form-label:before {\n      border-color: @success !important;\n    }\n  }\n  &.invalid {\n    & ~ .form-label:before {\n      border-color: @error !important;\n    }\n  }\n}\n\n// animation\n\n.checkbox, .radio {\n  &:before {\n    transform: scale(.9);\n    transition: all @time-small @ease-out;\n  }\n  &:after {\n    opacity: 0;\n    transform: scale(.9);\n    transition: all @time-small @ease-out;\n  }\n  &.active {\n    &:before {\n      transform: scale(1);\n      transition: all @time-small @ease-in;\n    }\n    &:after {\n      opacity: 1;\n      transform: scale(1);\n      transition: all @time-small @ease-in;\n    }\n  }\n  &.focus, &:focus {\n    &.form-label.form-label-default, .form-default & {\n      &:before {\n        border: 1px solid @accent;\n      }\n    }\n    &.form-label.form-label-inverse, .form-inverse & {\n      &:before {\n        border: 1px solid @shade-900;\n      }\n    }\n  }\n}\n\n/**\n * form-note\n */\n\n.form-note {\n  .note();\n  .note-default();\n  .note-small();\n  .muted();\n  .margin(y, small);\n  .margin(y, layout);\n}\n\n/**\n * validation\n */\n\n.form-item {\n  &:invalid {\n    box-shadow: none !important;\n  }\n}\n"