export default "/**\n * sidebar\n */\n\n// .gatsby_site_article_sidebar\n\n@gatsby_site_article_sidebar_width: 30rem;\n\n.gatsby_site_article_sidebar {\n  position: fixed;\n  z-index: 2;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  background: linear-gradient(-90deg, @grad-accent);\n  color: @white;\n  @media @max-md {\n    .sticky-disable();\n  }\n  @media @min-md {\n    width: @gatsby_site_article_sidebar_width;\n  }\n  overflow-x: hidden;\n  overflow-y: scroll;\n  .overflow-style(@mode: inner) !important;\n  line-height: 1;\n}\n\n/**\n * header\n */\n\n// .gatsby_site_header\n\n.gatsby_site_header {\n  .padding(top, 3rem);\n  .padding(bottom, medium);\n  .padding(x, 3rem);\n  color: @white;\n}\n\n.gatsby_site_header_logo_outer {\n  .list();\n  .list-space(small);\n  align-items: baseline;\n  justify-content: space-between;\n}\n\n.gatsby_site_header_top_social {\n  .list();\n  .list-space(tiny);\n}\n\n.gatsby_btn-site_header_top_social {\n  .link-none();\n  .btn-nodesign();\n  padding: 0;\n  border-radius: 0;\n  color: @text-inverse;\n  line-height: 1;\n  text-transform: uppercase;\n  opacity: 0.8;\n  &:hover, &.hover, &:active, &.active {\n    color: @text-inverse-dark;\n    opacity: 1;\n  }\n  &:active, &.active {\n    transform: scale(1.2);\n  }\n}\n\n.gatsby_site_header_links {\n  .margin(top, medium);\n  .padding(y, medium);\n  border-top: 1px solid fade(@shade-900, 25%);\n  border-bottom: 1px solid fade(@shade-900, 25%);\n}\n\n.gatsby_btn-site_header_link {\n  .full();\n  .link-none();\n  .btn-noborder();\n  box-shadow: none !important;\n  margin-top: 2px;\n  margin-bottom: 2px;\n  padding: .4em .75em;\n  border-color: transparent;\n  background: transparent;\n  .font-sans-semibold();\n  font-size: 1.3rem;\n  color: fade(@white, 50%);\n  line-height: 1;\n  text-transform: uppercase;\n  &:hover, &.hover {\n    border-color: transparent;\n    background: fade(@shade-900, 25%);\n    color: @text-inverse-dark;\n    transform: translateX(.25rem);\n  }\n  &:active, &.active, &.current {\n    border-color: transparent;\n    background: fade(@shade-900, 50%);\n    color: @text-inverse-dark;\n    transform: none;\n  }\n  // animation\n  transition: border @time-tiny @ease-out, background @time-tiny @ease-out, color @time-tiny @ease-out, transform @time-tiny @ease-out;\n  &:hover, &.hover {\n    transition: border @time-tiny @ease-in, background @time-tiny @ease-in, color @time-tiny @ease-in, transform @time-tiny @ease-in;\n  }\n  &:active, &.active {\n    transition: border @time-tiny @ease-in, background @time-tiny @ease-in, color @time-tiny @ease-in, transform @time-tiny @ease-in;\n  }\n}\n\n// .gatsby_site_header_listing\n\n.gatsby_site_header_listing {\n  .padding(bottom, 3rem);\n  .padding(x, 3rem);\n}\n\n.gatsby_site_header_cat {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  justify-content: flex-start;\n}\n\n.gatsby_site_header_inner,\n.gatsby_site_header_cat,\n.gatsby_site_header_item,\n.gatsby_site_header_adiacent {\n  position: relative;\n  margin-top: 1px;\n}\n\n.gatsby_site_header_sub, .gatsby_site_header_adiacent {\n  display: none;\n  width: 100%;\n  &.active {\n    display: block;\n  }\n}\n\n.gatsby_site_header_sub {\n  .gatsby_site_header_item {\n    position: relative;\n  }\n}\n\n.gatsby_site_header_adiacent {\n  .gatsby_site_header_item {\n    position: relative;\n    .padding(left, 8px);\n  }\n}\n\n.gatsby_cat--site_article_sidebar {\n  .h6();\n  margin: 0;\n  padding: 1rem 0;\n  color: fade(@white, 80%);\n}\n\n.gatsby_btn-site_article_sidebar {\n  pointer-events: auto !important;\n  .full();\n  .link-none();\n  box-shadow: none !important;\n  margin-bottom: 1px;\n  padding: .3em .5em;\n  border-color: transparent;\n  background: transparent;\n  .font-sans-regular();\n  font-size: 1.2rem;\n  color: fade(@white, 50%);\n  line-height: 1;\n  text-transform: none;\n  &:hover, &.hover {\n    border-color: transparent;\n    background: fade(@shade-900, 25%);\n    color: @text-inverse-dark;\n    transform: translateX(.25rem);\n  }\n  &:active, &.active, &.current {\n    border-color: transparent;\n    background: fade(@shade-900, 50%);\n    color: @text-inverse-dark;\n    transform: none;\n  }\n  // animation\n  transition: border @time-tiny @ease-out, background @time-tiny @ease-out, color @time-tiny @ease-out, transform @time-tiny @ease-out;\n  &:hover, &.hover {\n    transition: border @time-tiny @ease-in, background @time-tiny @ease-in, color @time-tiny @ease-in, transform @time-tiny @ease-in;\n  }\n  &:active, &.active {\n    transition: border @time-tiny @ease-in, background @time-tiny @ease-in, color @time-tiny @ease-in, transform @time-tiny @ease-in;\n  }\n}\n"