export default "import path from 'path'\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  component: dirs.pop(),\n  type: dirs.pop(),\n}\n\ndemo.htmlSource = `\n<div class=\"row row-default align-content-stretch\" style=\"height: 150px;\">\n\n  <div class=\"col-4\"></div>\n  <div class=\"col-8\"></div>\n  <div class=\"col-8\"></div>\n  <div class=\"col-4\"></div>\n\n</div>\n`\n"