export default "/**\n * demo\n */\n\n#gatsby_body-inner {\n  position: relative;\n  height: 100vh;\n}\n\nhtml.gatsby_iframe-inside {\n  body {\n    .overflow-style(@mode: none);\n    background: @white !important;\n  }\n  #gatsby_body-inner {\n    height: initial;\n  }\n}\n\nhtml.gatsby_iframe-container {\n  #gatsby_body-inner {\n    .padding(y, big);\n    .container();\n  }\n}\n\n.gatsby_demo_source {\n  .padding(all, medium);\n  #gatbsy_open-full & {\n    .padding(y, big);\n    .padding(x, none);\n    .container();\n  }\n  .gatsby_demo_item--full & {\n    .padding(all, none) !important;\n    max-width: none !important;\n  }\n}\n\n/**\n * code\n */\n\ncode {\n  margin: -.1em .1em;\n  padding: .2em .35em;\n  border-radius: 6px;\n  background: @shade-200 !important;\n  .font-monospace();\n  font-size: .8em;\n  color: @text;\n}\n\n// pre\n\npre {\n  overflow-x: hidden !important;\n  overflow-y: auto !important;\n  .overflow-style(@mode: inner);\n  .margin(y, medium) !important;\n  .margin(y, layout) !important;\n  .padding(y, small) !important;\n  .padding(x, small) !important;\n  width: 100%;\n  border-radius: 6px;\n  border-width: 0;\n  border-style: solid;\n  border: 3px solid @shade-200;\n  background: @shade-200 !important;\n  line-height: 0 !important;\n  code {\n    width: 100%;\n    background: transparent !important;\n    .font-monospace();\n    font-size: 1.2rem !important;\n    .line-height-medium() !important;\n    white-space: pre-wrap !important;\n  }\n  // animation\n  transition: border-color @time-small @ease-out;\n  &:hover, &.hover, &:active, &.active {\n    border-color: @shade-200-dark;\n    transition: border-color @time-small @ease-in;\n  }\n}\n\n/**\n * svg\n */\n\n/* WTF is this\nsvg {\n  .font-sans-semibold();\n  font-size: 1.8rem;\n  text-transform: uppercase;\n}\n*/\n\n/**\n * .gatsby_demo-cols\n */\n\n.gatsby_demo-cols {\n  [class^='col-'], [class*=' col-'], [class^='demo--col-'], [class*=' demo--col-'] {\n    &:before {\n      position: relative;\n      float: left;\n      width: 100%;\n      height: 22px;\n      min-height: 100%;\n      border: 1px solid darken(@shade-200, 5%);\n      background: @shade-200;\n      content: attr(data-index);\n      .font-sans-semibold();\n      font-size: 12px;\n      color: @accent;\n      line-height: 1.8;\n      text-align: center;\n    }\n  }\n}\n\n.gatsby_demo-cols-nested {\n  [class^='col-'], [class*=' col-'] {\n    .gatsby_demo-cols();\n  }\n}\n\n/**\n * .demo\n */\n\n@gatsby_demo-height: 55rem;\n\npre {\n  max-height: @gatsby_demo-height;\n}\n\n.gatsby_demo {\n  .margin(bottom, medium);\n  .margin(bottom, layout);\n  border-radius: 6px;\n  pre {\n    border-width: 0 !important;\n  }\n  // animation\n  border: 3px solid @shade-200;\n  transition: border-color @time-small @ease-out;\n  .gatsby_demo_tabs {\n    overflow: hidden;\n  }\n  .gatsby_demo_tabs_right {\n    > * {\n      opacity: 0;\n      transform: translateY(1.5rem);\n      transition: opacity @time-small @ease-in, transform @time-small @ease-in;\n      transition-delay: 0ms; // @start + @child * @increment\n      .nth-child(@child: 3, @start: 150ms, @increment: 0ms, @decrement: 50ms, {\n        @transition-delay();\n      });\n    }\n  }\n  &:hover, &:focus-within {\n    border-color: @shade-200-dark;\n    transition: border-color @time-small @ease-in;\n    .gatsby_demo_tabs_right {\n      > * {\n        opacity: 1;\n        transform: translateY(0);\n        transition: opacity @time-small @ease-in, transform @time-small @ease-in;\n      }\n    }\n    .gatsby_demo_tabs, .gatsby_demo_code_tabs {\n      .btn {\n        &:active, &.active {\n          border-color: @shade-200-dark !important;\n        }\n      }\n    }\n  }\n  @media (hover: none) {\n    .gatsby_demo_tabs_right {\n      > * {\n        opacity: 1 !important;\n        .transform-none() !important;\n        .transition-none() !important;\n      }\n    }\n  }\n  #gatbsy_open-full & {\n    border: 0 !important;\n    .btn-prev-demo, .btn-next-demo {\n      display: inline-flex;\n    }\n    @media @max-sm {\n      .btn-prev-demo, .btn-next-demo {\n        display: none;\n      }\n    }\n    .gatsby_demo_code_body,\n    .gatsby_demo_item:not(.populated-iframe) {\n      overflow-x: hidden;\n      overflow-y: auto;\n      .overflow-style(@mode: none);\n    }\n    .gatsby_demo_item.active-code {\n      overflow-x: hidden;\n      overflow-y: hidden;\n    }\n    .gatsby_demo_tabs_right {\n      > * {\n        opacity: 1 !important;\n        .transform-none() !important;\n        .transition-none() !important;\n      }\n    }\n  }\n}\n\n.gatsby_demo_tabs, .gatsby_demo_code_tabs, .gatsby_demo_code_body {\n  background: @shade-200;\n}\n\n.gatsby_demo_tabs, .gatsby_demo_code_tabs {\n  position: relative;\n  display: flex;\n  align-items: flex-start;\n  justify-content: space-between;\n  > * {\n    .list();\n    .list-default();\n    .list-space(mini);\n  }\n  .padding(all, 7px);\n  .btn {\n    border-width: 3px;\n    padding: .5em .75em;\n    font-size: 10px;\n    color: fade(@text, 50%);\n    &:hover, &.hover, &:active, &.active {\n      border-color: @shade-200;\n      background: @white;\n      color: @text;\n    }\n  }\n  .gatsby_demo_tabs_right {\n    flex-wrap: nowrap;\n    .btn {\n      padding-top: .6em;\n      padding-bottom: .6em;\n      [class^='icon-'], [class*=' icon-'] {\n        position: relative;\n        top: .05em;\n      }\n    }\n  }\n}\n\n.gatsby_demo_tabs_inner {\n  margin-top: -3px;\n}\n\n.gatsby_demo_text {\n  .font-small();\n  .padding(y, small);\n  .padding(x, medium);\n  border-bottom: 1px solid @shade-200;\n  code {\n    background: @shade-200 !important;\n  }\n}\n\n.gatsby_demo_source {\n  &:not(.gatsby_demo_source--from) {\n    display: none !important;\n  }\n}\n\n.gatsby_demo_item {\n  position: relative;\n  /* NO because drop go below next demo item\n  > * {\n    z-index: 10;\n    &.gatsby_demo_source {\n      z-index: 9;\n    }\n  }\n  */\n  .toggle-flex() !important;\n  .gatsby_demo_item_wrapper {\n    position: relative;\n    #gatbsy_open-full & {\n      height: auto !important;\n    }\n  }\n  .gatsby_demo_code {\n    .toggle-block();\n    #gatbsy_open-full & {\n      position: absolute;\n      width: 100%;\n      height: 100%;\n    }\n    .gatsby_demo_code_inner {\n      display: flex;\n      flex-flow: column;\n      height: 100%;\n    }\n    &.active {\n      z-index: 11;\n    }\n  }\n  &[data-iframe] {\n    &:not(.loaded) {\n      iframe {\n        visibility: hidden;\n      }\n    }\n    &.loaded {\n      .gatsby_demo_item_wrapper > .loader {\n        display: none;\n      }\n    }\n    iframe {\n      display: block;\n      width: 100%;\n      height: 0;\n      border: 0;\n      &:not(.gatsby_iframe-full) {\n        height: @gatsby_demo-height;\n      }\n    }\n  }\n  &:not(.populated) {\n    .gatsby_demo_code_body {\n      width: 100%;\n      overflow: hidden;\n    }\n  }\n  &.populated {\n    .gatsby_demo_code_body_item {\n      .toggle-block();\n    }\n  }\n}\n\n.gatsby_demo_code_body {\n  z-index: 1;\n  flex: 1;\n}\n\ntable {\n  border-color: @shade-200; // @FIX initial color\n}\n\n.gatbsy_table {\n  border-color: @shade-200;\n  transition: border-color @time-small @ease-out;\n  &:hover, &.hover, &:active, &.active {\n    border-color: @shade-200-dark;\n    transition: border-color @time-small @ease-in;\n  }\n  td {\n    code {\n      white-space: nowrap;\n    }\n  }\n}\n\n// theme\n\n[data-gatsby-listing-toggle] + .gatsby_demo {\n  display: none;\n  .gatsby_demo_source_populate {\n    display: inline-flex; // populate source disable\n  }\n}\n\n.gatsby_demo, .gatsby_demo_item, .gatsby_demo_item_wrapper, .gatsby_demo_item_wrapper iframe {\n  display: flex;\n  flex-direction: column;\n  #gatbsy_open-full & {\n    .flex-full();\n    width: 100%;\n  }\n}\n\n#gatbsy_open-full {\n  position: fixed;\n  z-index: @index-overlay;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  background: @white;\n  @media @min-md {\n    margin-left: @gatsby_site_article_sidebar_width;\n  }\n  .btn-close {\n    top: 0;\n    right: 0;\n    .btn-primary();\n    .btn-squared();\n    font-size: 15px;\n  }\n  .gatsby_demo_tabs_right {\n    padding-right: 4rem;\n  }\n  .btn-open-full {\n    display: none !important;\n  }\n}\n\n#gatbsy_open-full-content {\n  display: flex;\n  height: 100vh;\n  pre {\n    max-height: none;\n  }\n}\n"