export default "#iframe--2020-bertani-hero {\n//##START\n/**\n  * slider\n  */\n\n.slider {\n  position: relative;\n  background: @shade-200;\n  .slides-inner {\n    .row-space(none);\n    // dragger animations\n    .transition-none() !important;\n    .transform-none() !important;\n  }\n  .slide {\n    position: relative;\n    .col(12);\n    // activation\n    display: none;\n    &.active, &.out {\n      display: block;\n      z-index: @index-active;\n    }\n    &.next {\n      display: block;\n      z-index: @index-active + 1;\n    }\n    &:not(.active) {\n      position: absolute;\n    }\n  }\n  .slider-pagination {\n    display: none;\n  }\n  // .slide_item\n  .slide_item {\n    .full();\n    display: block;\n    overflow: hidden;\n  }\n  .slide_asset {\n    .media-container {\n      height: 100vh;\n      height: calc(var(--vh, 1vh) * 100);\n    }\n    &:before {\n      content: '';\n      position: absolute;\n      z-index: 2;\n      left: 0;\n      bottom: 0;\n      right: 0;\n      height: 75%;\n      background: linear-gradient(180deg, fade(@black, 0%) 0%, fade(@black, 30%) 100%);\n    }\n  }\n  .slide_text {\n    .link-none();\n    position: absolute;\n    z-index: 3;\n    bottom: 0;\n    left: 0;\n    right: 0;\n  }\n  .slide_text_inner {\n    .text-inverse();\n    .padding(right, big);\n    .padding(y, giant);\n    max-width: 80rem;\n  }\n  // line\n  .slide_line {\n    .container-space();\n    pointer-events: none;\n    position: absolute;\n    z-index: 1;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n  }\n  .slide_line_inner {\n    position: relative;\n    width: 100%;\n    height: 100%;\n  }\n  .slide_line_small {\n    position: absolute;\n    bottom: 0;\n    right: 0;\n    height: 200px;\n    width: 1px;\n    background: @white;\n  }\n  .slide_line_big {\n    position: absolute;\n    margin-right: -1px;\n    bottom: 80px;\n    right: 0;\n    height: 120px;\n    width: 3px;\n    background: @white;\n  }\n  // .slider-navigation-sides\n  .slider-navigation-sides {\n    color: @white;\n  }\n}\n//##END\n}\n"