export default "@import 'xtend-library/src/core/row/_row.less';\n\n/**\n * vars\n */\n\n// generate class: [false], [true] non responsive only, [sm, md, lg] responsive @breakpoints\n\n@generate-row: true;\n\n// row\n\n@row-spaces: none, mini, tiny, small, medium, large, big, giant; // if you change this change also variables names below\n@row-space-none: 0 0; // vertical horizontal\n@row-space-mini: .25rem .25rem;\n@row-space-tiny: .5rem .5rem;\n@row-space-small: 1rem 1rem;\n@row-space-medium: 1.5rem 1.5rem;\n@row-space-large: 2rem 2rem;\n@row-space-big: 3rem 3rem;\n@row-space-giant: 4rem 4rem;\n@row-space-huge: 6rem 6rem;\n\n/**\n * row\n */\n\n// variant\n\n.row-default {\n  .row-space(medium);\n}\n\n.row-form {\n  .row-space-x(medium);\n}\n\n// other\n\n.row-touch-overflow {\n  @media (hover: none) {\n    flex-wrap: nowrap;\n    overflow-x: auto;\n    overflow-y: hidden;\n    .overflow-style(@mode: inner);\n    > * {\n      .col-custom(30rem) !important;\n    }\n  }\n}\n"