export default "/**\n * check and radio card\n */\n\n// setup\n\n.checkbox-card, .radio-card {\n  position: relative !important;\n  &:not(.disabled) {\n    cursor: pointer;\n  }\n  // label\n  .form-label();\n  .checkradio-label() !important;\n  // check and radio styled design\n  &:before, &:after {\n    content: '';\n    position: absolute !important;\n    top: 0;\n    left: 0;\n  }\n  &:before {\n    pointer-events: none;\n  }\n  &:after {\n    display: flex !important;\n    align-items: center;\n    justify-content: center;\n  }\n}\n\n// styled\n\ninput[type=\"checkbox\"]:not(.unstyled) {\n  & ~ * .form-label {\n    .checkbox-card();\n  }\n}\n\ninput[type=\"radio\"]:not(.unstyled) {\n  & ~ * .form-label {\n    .radio-card();\n  }\n}\n\n// pseudo\n\ninput[type=\"checkbox\"]:not(.unstyled) {\n  // focus\n  &:focus ~ * .form-label {\n    .checkbox-card.focus();\n  }\n  // active\n  &:checked ~ * .form-label {\n    .checkbox-card.active();\n  }\n  &:checked ~ * .card-design {\n    border-width: 2px;\n    border-color: @accent;\n  }\n  // disabled\n  &:disabled ~ * .form-label {\n    .disabled();\n  }\n}\n\ninput[type=\"radio\"]:not(.unstyled) {\n  // focus\n  &:focus ~ * .form-label {\n    .radio-card.focus();\n  }\n  // active\n  &:checked ~ * .form-label {\n    .radio-card.active();\n  }\n  &:checked ~ * .card-design {\n    border-width: 2px;\n    border-color: @accent;\n  }\n  // disabled\n  &:disabled ~ * .form-label {\n    .disabled();\n  }\n}\n\n/**\n  * styles\n  */\n\n// styled\n\n.checkbox-card, .radio-card {\n  padding-left: 3rem;\n  &, &:before, &:after {\n    width: 3.6rem;\n    height: 3.6rem;\n  }\n  // variant\n  &:before {\n    background: @white;\n    border: 2px solid #D3D3D3;\n  }\n  &:after {\n    color: @white;\n  }\n}\n\n.checkbox-card {\n  &:before {\n    border-radius: 2px;\n  }\n  &:after {\n    .icon-xt();\n    content: @icon-xt-check;\n    font-size: 150%;\n  }\n}\n\n.radio-card {\n  &:before {\n    border-radius: 50%;\n  }\n  &:after {\n    .icon-xt();\n    content: @icon-xt-circle;\n    font-size: 100%;\n  }\n}\n\n// validation\n\ninput[type=\"checkbox\"], input[type=\"radio\"] {\n  &.valid {\n    & ~ * .form-label:before {\n      border-color: @success !important;\n    }\n  }\n  &.invalid {\n    & ~ * .form-label:before {\n      border-color: @error !important;\n    }\n  }\n}\n\n// animation\n\n.checkbox-card, .radio-card {\n  &:before {\n    transform: scale(.9);\n    transition: all @time-small @ease-out;\n  }\n  &:after {\n    opacity: 0 !important;\n    transform: scale(.9);\n    transition: all @time-small @ease-out;\n  }\n  &.focus, &:focus {\n    &:before {\n      border: 1px solid @accent;\n    }\n  }\n  &.active {\n    &:before {\n      background: @accent;\n      transform: scale(1);\n      transition: all @time-small @ease-in;\n    }\n    &:after {\n      opacity: 1 !important;\n      transform: scale(1);\n      transition: all @time-small @ease-in;\n    }\n  }\n}\n"