export default "#iframe--2020-aniyeby-hero {\n//##START\n/**\n  * slider\n  */\n\n.slider {\n  position: relative;\n  background: @shade-200;\n  .slides-inner {\n    .row-space(none);\n    // dragger animations\n    .transition-none() !important;\n    .transform-none() !important;\n  }\n  .slide {\n    position: relative;\n    .col(12);\n    // activation\n    display: none;\n    &.active {\n      display: block;\n      z-index: @index-active;\n    }\n    &.out {\n      display: block;\n    }\n    &:not(.active) {\n      position: absolute;\n    }\n  }\n  .slider-pagination {\n    display: none;\n  }\n  // .slide_item\n  .slide_item {\n    .link-none();\n    .full();\n    display: block;\n    overflow: hidden;\n  }\n  .slide_cover {\n    pointer-events: none;\n    position: absolute;\n    z-index: @index-active;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background: @accent-dark;\n  }\n  .slide_asset {\n    .media-container {\n      height: 100vh;\n      height: calc(var(--vh, 1vh) * 100);\n    }\n  }\n  // .slider-navigation-sides\n  .slider-navigation-sides {\n    color: @white;\n  }\n}\n//##END\n}\n"