export default "/**\n * card-separator\n */\n\n.card {\n  .card-separator& {\n    > .card-inner > .card-content {\n      > .card-item {\n        & + .card-item {\n          &:before {\n            content: '';\n            position: absolute;\n            top: 0;\n            bottom: auto;\n            left: 0;\n            right: 0;\n            width: auto;\n            height: 1px;\n          }\n        }\n      }\n    }\n    > .card-inner > .card-content > .card-item-nested {\n      > .card-item {\n        & + .card-item {\n          &:before {\n            content: '';\n            position: absolute;\n            top: 0;\n            left: 0;\n            bottom: 0;\n            right: auto;\n            width: 1px;\n            height: auto;\n          }\n        }\n      }\n    }\n  }\n}\n\n// variant\n\n.card-default {\n  .card-separator& {\n    .card-contents({\n      > .card-item {\n        & + .card-item {\n          &:before {\n            background: fade(@shade-900, 10%);\n          }\n        }\n      }\n    });\n  }\n}\n\n.card-primary {\n  .card-separator& {\n    .card-contents({\n      > .card-item {\n        & + .card-item {\n          &:before {\n            background: fade(@shade-200, 10%);\n          }\n        }\n      }\n    });\n  }\n}\n\n// special\n\n.card-group, .card-horizontal {\n  .card-separator& {\n    .card-contents({\n      > .card-item {\n        & + .card-item {\n          &:before {\n            top: 0;\n            left: 0;\n            bottom: 0;\n            right: auto;\n            width: 1px;\n            height: auto;\n          }\n        }\n      }\n    });\n  }\n}\n"