export default "import path from 'path'\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  component: dirs.pop(),\n  type: dirs.pop(),\n}\n\ndemo.htmlSource = `\n<div class=\"list list-default list-space-small align-items-center justify-content-center\">\n\n  <div class=\"card card-primary\">\n    <div class=\"card-design\"></div>\n    <div class=\"card-inner\">\n      <div class=\"card-content\">\n        <div class=\"card-block card-item\">\n          <div class=\"card-title\">Lorem ipsum dolor sit amet</div>\n          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin molestie diam nec euismod commodo. Nunc ut fringilla nibh. Duis quis arcu quis neque tempor lobortis nec nec mauris. Proin vel elit pretium metus egestas congue. Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia Curae; Quisque hendrerit sagittis quam eget elementum. Vestibulum eu nulla nisl. Duis nec commodo tortor. Aenean feugiat, libero eget ultricies viverra, justo nunc efficitur lorem, at aliquet ante eros in est.</p>\n        </div>\n        <div class=\"loader loader-x loader-size-x loader-background\">\n          <div class=\"filler\">\n            <span></span><span></span>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n\n  <button type=\"button\" class=\"btn btn-default\">\n    Lorem ipsum\n    <span class=\"loader loader-x loader-size-x loader-background\">\n      <span class=\"filler\">\n        <span></span><span></span>\n      </span>\n    </span>\n  </button>\n\n</div>\n`\n"