export default "@import 'xtend-library/src/core/typography/_typography.less';\n\n/**\n * font face\n */\n\n@import (css) url(https://fonts.googleapis.com/css?family=Open+Sans:400,600);\n\n.font-sans-regular() {\n  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;\n  font-weight: 400;\n  font-style: normal;\n}\n\n.font-sans-semibold() {\n  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;\n  font-weight: 600;\n  font-style: normal;\n}\n\n.font-monospace() {\n  font-family: monaco, Consolas, 'Lucida Console', monospace;\n  font-weight: normal;\n  font-style: normal;\n}\n\n/**\n * rem\n */\n\n// rem\n\nhtml {\n  // base font size for rem\n  // 100% is 16px\n  // value(%) = desired(px) * 100 / 16\n  font-size: 56.25%; // 9px\n  @media @min-md {\n    font-size: 62.5%; // 10px\n  }\n  @media @min-xl {\n    font-size: 68.75%; // 11px\n  }\n}\n\nbody {\n  .font-sans-regular();\n  .font-medium();\n  .line-height-medium();\n  color: @text;\n  font-variant-ligatures: none;\n}\n\n/**\n * spacing\n */\n\n.spacing-none {\n  .margin(top, none);\n  .margin(bottom, none);\n}\n\n.spacing-mini {\n  .margin(top, mini);\n  .margin(bottom, micro);\n  .margin(y, layout);\n}\n\n.spacing-tiny {\n  .margin(top, tiny);\n  .margin(bottom, mini);\n  .margin(y, layout);\n}\n\n.spacing-small {\n  .margin(top, small);\n  .margin(bottom, tiny);\n  .margin(y, layout);\n}\n\n.spacing-medium {\n  .margin(top, medium);\n  .margin(bottom, small);\n  .margin(y, layout);\n}\n\n.spacing-large {\n  .margin(top, large);\n  .margin(bottom, medium);\n  .margin(y, layout);\n}\n\n.spacing-big {\n  .margin(top, big);\n  .margin(bottom, large);\n  .margin(y, layout);\n}\n\n.spacing-giant {\n  .margin(top, giant);\n  .margin(bottom, big);\n  .margin(y, layout);\n}\n\n.spacing-huge {\n  .margin(top, huge);\n  .margin(bottom, giant);\n  .margin(y, layout);\n}\n\n/**\n * header\n */\n\nh1, .h1 {\n  .spacing-large();\n  .font-sans-semibold();\n  .line-height-tiny();\n  font-size: 4rem;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\nh2, .h2 {\n  .spacing-large();\n  .font-sans-semibold();\n  .line-height-tiny();\n  font-size: 3.5rem;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\nh3, .h3 {\n  .spacing-large();\n  .font-sans-semibold();\n  .line-height-tiny();\n  font-size: 2.5rem;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\nh4, .h4 {\n  .spacing-medium();\n  .font-sans-semibold();\n  .line-height-small();\n  font-size: 2rem;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\nh5, .h5 {\n  .spacing-medium();\n  .font-sans-semibold();\n  .line-height-small();\n  font-size: 1.7rem;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\nh6, .h6 {\n  .spacing-medium();\n  .font-sans-semibold();\n  .line-height-small();\n  font-size: 13px;\n  letter-spacing: .025em;\n  text-transform: uppercase;\n}\n\n// special\n\nh1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {\n  & + & { // sequential spacing\n    .margin(top, medium) !important;\n  }\n}\n\n.header-line {\n  .spacing-big();\n  .padding(bottom, small);\n  border-bottom: 3px solid @shade-200;\n}\n\n.header-block {\n  .spacing-big();\n  .padding(y, small);\n  .padding(x, medium);\n  background: @shade-200;\n  text-align: center;\n}\n\n/**\n * text\n */\n\np {\n  .margin(bottom, medium);\n  .margin(bottom, layout);\n}\n\nstrong {\n  .font-sans-semibold();\n}\n\nsmall {\n  font-size: .8em;\n}\n\n.muted {\n  opacity: .5;\n}\n\n.align-uppercase {\n  position: relative;\n  top: -.1em;\n}\n\n// text\n\n.text-default {\n  color: @text;\n}\n\n.text-inverse {\n  color: @text-inverse;\n}\n\n// font\n\n.font-micro {\n  font-size: 11px;\n}\n\n.font-mini {\n  font-size: 12px;\n}\n\n.font-tiny {\n  font-size: 1.3rem;\n}\n\n.font-small {\n  font-size: 1.4rem;\n}\n\n.font-medium {\n  font-size: 1.5rem;\n}\n\n.font-large {\n  font-size: 1.6rem;\n}\n\n.font-big {\n  font-size: 1.7rem;\n}\n\n.font-giant {\n  font-size: 1.8rem;\n}\n\n.font-huge {\n  font-size: 1.9rem;\n}\n\n// line-height\n\n.line-height-tiny {\n  line-height: 1.2;\n}\n\n.line-height-small {\n  line-height: 1.4;\n}\n\n.line-height-medium {\n  line-height: 1.6;\n}\n\n.line-height-large {\n  line-height: 1.8;\n}\n\n.line-height-big {\n  line-height: 2;\n}\n\n.line-height-giant {\n  line-height: 2.25;\n}\n\n.line-height-huge {\n  line-height: 2.5;\n}\n\n/**\n * ul ol\n */\n\nul, ol {\n  .margin(top, none);\n  .margin(bottom, medium);\n  padding-left: 2rem;\n  > li {\n    .margin(bottom, small);\n    > ul, > ol {\n      .margin(y, small);\n    }\n  }\n}\n\n// .list-reset\n// use this mixin to reset styles on xtend ul.list ul.slides-inner etc..\n\n.list-reset() {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n  > li {\n    margin: 0;\n    padding: 0;\n    background: none;\n  }\n}\n\n/**\n * dl\n */\n\ndl {\n  .margin(bottom, medium);\n  .margin(bottom, layout);\n  .dl-block();\n  dt {\n    .font-sans-regular();\n  }\n  dd {\n    .font-sans-semibold();\n  }\n}\n\n// .dl-block\n\n.dl-block {\n  dt {\n    .padding(top, small);\n  }\n  dd {\n    .padding(top, tiny);\n  }\n}\n\n// .dl-line\n\n.dl-line {\n  dt {\n    .padding(top, none);\n  }\n  dd {\n    .padding(top, none);\n    .padding(left, small);\n  }\n}\n\n// .dl-horizontal\n\n.dl-horizontal {\n  text-align: left;\n  .margin-remove(top, tiny);\n  dt {\n    .padding(top, tiny);\n    .padding(right, small);\n    float: left;\n    clear: left;\n    width: 120px;\n  }\n  dd {\n    .padding(left, small);\n    .padding(top, tiny);\n    margin-left: 120px;\n  }\n}\n"