export default "import path from 'path'\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  component: dirs.pop(),\n  type: dirs.pop(),\n}\n\ndemo.htmlSource = `\n<form class=\"form-default\">\n  <div class=\"demo--form-mixins\">\n    <div>\n      <label>\n        Input\n      </label>\n      <input type=\"text\" placeholder=\"Input\">\n    </div>\n  \n    <div>\n      <label>\n        File\n      </label>\n      <input type=\"file\"/>\n    </div>\n  \n    <div>\n      <label>\n        Textarea\n      </label>\n      <textarea placeholder=\"Textarea\"></textarea>\n    </div>\n  \n    <div>\n      <label>\n        Select\n      </label>\n      <select>\n        <option selected value=\"\">Select an option</option>\n        <option>Test</option>\n        <option>Test</option>\n        <option>Test</option>\n      </select>\n    </div>\n  \n    <div>\n      <label>\n        Select multiple\n      </label>\n      <select multiple>\n        <option>Test</option>\n        <option>Test</option>\n        <option>Test</option>\n      </select>\n    </div>\n  \n    <button type=\"submit\" class=\"btn btn-primary\">\n      submit\n    </button>\n  </div>\n</form>\n`\n"