export default "/**\n * sticky\n */\n\n.xt-sticky {\n  // default\n  &.xt-clone {\n    pointer-events: none;\n    position: relative;\n    visibility: hidden;\n    top: 0;\n    width: 100%;\n  }\n  &:not(.xt-clone) {\n    position: relative;\n    width: 100%;\n  }\n  // mode default\n  &.xt-clone:not(.active) {\n    position: absolute;\n  }\n  &:not(.xt-clone):not(.active) {\n    position: relative;\n  }\n  // showhide\n  .show-sticky {\n    display: none;\n  }\n  .hide-sticky {\n    display: block;\n  }\n  &:not(.xt-clone).active {\n    .show-sticky {\n      display: block;\n    }\n    .hide-sticky {\n      display: none;\n    }\n  }\n  // activation\n  &:not(.xt-clone).active {\n    position: fixed;\n    z-index: @index-sticky;\n    width: 100%;\n  }\n}\n\n.xt-sticky-absolute {\n  // mode absolute\n  &.xt-clone {\n    position: relative;\n  }\n  &.xt-clone:not(.active) {\n    position: relative;\n  }\n  &:not(.xt-clone):not(.active) {\n    position: relative;\n  }\n  &.xt-fixed:not(.xt-clone):not(.active) {\n    position: absolute;\n    width: 100%;\n  }\n}\n\n.xt-sticky-fixed {\n  // mode default\n  &.xt-clone {\n    position: relative;\n  }\n  &.xt-clone:not(.active) {\n    position: relative;\n  }\n  &:not(.xt-clone):not(.active) {\n    position: relative;\n  }\n  &.xt-fixed:not(.xt-clone):not(.active) {\n    position: fixed;\n    z-index: @index-sticky;\n    width: 100%;\n  }\n}\n\n.xt-sticky-fixed-always {\n  // mode default\n  &.xt-clone {\n    position: absolute;\n  }\n  &.xt-clone:not(.active) {\n    position: absolute;\n  }\n  &:not(.xt-clone):not(.active) {\n    position: fixed;\n    z-index: @index-sticky;\n    width: 100%;\n  }\n  &.xt-fixed:not(.xt-clone):not(.active) {\n    position: fixed;\n    z-index: @index-sticky;\n    width: 100%;\n  }\n}\n\n// .sticky-disable\n\n.sticky-disable {\n  .xt-disable();\n  position: relative !important;\n  top: auto !important;\n  bottom: auto !important;\n  &.xt-clone {\n    position: absolute !important;\n  }\n  &:not(.xt-clone) {\n    position: relative !important;\n  }\n}\n"