export default "/**\n * card-collapse\n */\n\n.card-collapse {\n  .card-contents({\n    > .card-item {\n      padding: 0 !important;\n    }\n  });\n}\n\n// size\n\n.card-small {\n  .card-collapse& {\n    > .card-inner > .card-content {\n      padding: 1.5rem 2rem;\n    }\n    .card-contents({\n      .list-space(small);\n    });\n  }\n}\n\n.card-medium {\n  .card-collapse& {\n    > .card-inner > .card-content {\n      padding: 2rem 2.5rem;\n    }\n    .card-contents({\n      .list-space(medium);\n    });\n  }\n}\n\n.card-large {\n  .card-collapse& {\n    > .card-inner > .card-content {\n      padding: 3rem 3.5rem;\n    }\n    .card-contents({\n      .list-space(large);\n    });\n  }\n}\n"