export default "#iframe--2020-euroricambi-cards {\n//##START\n/**\n  * slider\n  */\n.slider {\n  .slides-inner {\n    overflow: hidden;\n    // dragger animations\n    .transition-none() !important;\n    .transform-none() !important;\n  }\n  .slide {\n    .toggle-block();\n    .col(12);\n  }\n  .slide_item {\n    position: relative;\n  }\n  .slide-pagination-content {\n    display: none;\n  }\n  // navigation\n  [data-xt-nav].btn {\n    .btn-default();\n    font-size: 10px;\n    [class^='icon-'], [class*=' icon-'] {\n      .icon-medium();\n    }\n  }\n  .slider--nonav {\n    [data-xt-nav].btn {\n      display: none;\n    }\n  }\n  // .slide_item\n  .slide_card {\n    @media @max-sm {\n      .container-space-remove();\n    }\n    @media @min-sm {\n      position: absolute;\n      bottom: 2.5rem;\n      right: 2.5rem;\n      width: 49rem;\n    }\n    > .card {\n      .card-squared();\n      align-items: flex-end;\n    }\n  }\n  .slide_card_inner {\n    overflow: hidden;\n  }\n  .slide_asset {\n    position: relative;\n    background: @shade-200;\n    .media-container {\n      overflow: hidden;\n      background: transparent;\n    }\n    .media-inner {\n      overflow: hidden;\n    }\n  }\n  .slide_assets--desktop {\n    > .row {\n      .row-space-y(none); // @FIX add row vertical space..\n    }\n    @media @max-sm {\n      display: none;\n    }\n    .slide_asset {\n      .media-container {\n        height: 49rem;\n      }\n    }\n  }\n  .slide_assets--mobile {\n    .container-space-remove();\n    @media @min-sm {\n      display: none;\n    }\n    .slide_asset {\n      .media-container {\n        height: 25rem;\n      }\n    }\n  }\n  // pagination\n  .slider-pagination {\n    .container-space();\n    .list-space(mini);\n    flex-wrap: nowrap;\n    align-items: center;\n    justify-content: center;\n    .padding(bottom, big);\n  }\n  [data-xt-pag].btn {\n    .btn-default();\n  }\n  // pagination wrap\n  .slider-pagination_inner {\n    position: relative;\n    overflow: hidden;\n    white-space: nowrap;\n    [data-xt-pag] {\n      .margin(x, mini);\n    }\n    &:before, &:after {\n      content: '';\n      pointer-events: none;\n      position: absolute;\n      z-index: @index-active + 1;\n      top: 0;\n      bottom: 0;\n      width: 30px;\n    }\n    &:before {\n      left: 0;\n      background: linear-gradient(-90deg, fade(@white, 0%) 0%, @white 100%);\n    }\n    &:after {\n      right: 0;\n      background: linear-gradient(90deg, fade(@white, 0%) 0%, @white 100%);\n    }\n  }\n  .slider-cards--logos {\n    .slider-pagination_inner {\n      max-width: none;\n    }\n  }\n  .slider-pagination_scroll {\n    display: inline-block;\n    position: relative;\n    .padding(x, 30px);\n    transition: left 750ms @ease-out;\n  }\n}\n//##END\n}\n"