export default "#iframe--2019-sun68-hero {\n//##START\n/**\n  * slider\n  */\n\n.slider {\n  background: @black;\n  .slides-inner {\n    .row-space(none);\n  }\n  .slide {\n    .col(12);\n  }\n  .slider-navigation {\n    color: @white;\n  }\n  .slider-pagination {\n    display: none;\n  }\n  // .slide_item\n  .slide_item {\n    .link-none();\n    display: block;\n  }\n  .slide_asset {\n    position: relative;\n    overflow: hidden;\n    width: 100%;\n    height: 100vh;\n    height: calc(var(--vh, 1vh) * 100);\n  }\n  .slide_content {\n    position: absolute;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    display: flex;\n    align-items: flex-end;\n    justify-content: center;\n    .padding(y, big);\n    color: @white;\n    text-align: center;\n    @media @min-sm {\n      justify-content: flex-start;\n      text-align: left;\n    }\n  }\n  // .slider-navigation-sides\n  .slider-navigation-sides {\n    color: @white;\n  }\n}\n//##END\n}\n"