export default "/**\n * icon\n */\n\n.icon-base() {\n  display: inline-block;\n  vertical-align: middle;\n  align-items: center;\n  position: relative;\n  font-size: @icon-medium;\n  speak: none;\n  font-style: normal;\n  font-weight: normal;\n  font-variant: normal;\n  line-height: inherit;\n  text-transform: none;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  img& {\n    top: -.1em;\n    width: auto;\n    height: @icon-medium;\n  }\n}\n\n/**\n * icon-xt\n */\n\n.icon-xt {\n  .icon-base();\n  font-family: @icons-xt-family;\n}\n\n[class^='icon-xt-'], [class*=' icon-xt-'] {\n  display: inline-block;\n  vertical-align: middle;\n  align-items: center;\n  &:before {\n    .icon-xt();\n  }\n}\n\n/**\n * icon\n */\n\n@icons-theme--family: none;\n@icons-theme: none;\n\n.icon-theme {\n  & when not (@icons-theme--family = none) {\n    .icon-base();\n    font-family: @icons-theme--family;\n  }\n}\n\n[class^='icon-theme-'], [class*=' icon-theme-'] {\n  display: inline-block;\n  vertical-align: middle;\n  align-items: center;\n  &:before {\n    .icon-theme();\n  }\n}\n\n/**\n * @generate-icon\n */\n\n// .icon-xt\n\n.icon-xt(@content, @size: @icon-medium, @position: after) {\n  & when (@position = before) {\n    &:before {\n      .icon-xt();\n      content: '@{content}';\n      font-size: @size;\n    }\n  }\n  & when (@position = after) {\n    &:after {\n      .icon-xt();\n      content: '@{content}';\n      font-size: @size;\n    }\n  }\n}\n\n// .icon\n\n.icon(@content, @size: @icon-medium, @position: after) {\n  & when (@position = before) {\n    &:before {\n      .icon-theme();\n      content: '@{content}';\n      font-size: @size;\n    }\n  }\n  & when (@position = after) {\n    &:after {\n      .icon-theme();\n      content: '@{content}';\n      font-size: @size;\n    }\n  }\n}\n\n// generate\n\n& when (@generate-icon) {\n\n  // generate-icons-xt\n\n  .generate-icons-xt();\n  .generate-icons-xt(@i: length(@icons-xt)) when (@i > 0) {\n    .generate-icons-xt(@i - 1);\n    @name: extract(@icons-xt, @i);\n    @val: ~\"icon-xt-@{name}\";\n    .icon-xt-@{name} {\n      &:before {\n        content: @@val;\n      }\n    }\n  }\n\n  // generate-icons\n\n  & when not (@icons-theme--family = none) {\n\n    .generate-icons();\n    .generate-icons(@i: length(@icons-theme)) when (@i > 0) {\n      .generate-icons(@i - 1);\n      @name: extract(@icons-theme, @i);\n      @val: ~\"icon-theme-@{name}\";\n      .icon-@{name} {\n        &:before {\n          content: @@val;\n        }\n      }\n    }\n\n  }\n\n}\n"