export default "/**\n * footer\n */\n\n// .gatsby_site_footer_bottom\n\n.gatsby_site_footer_bottom {\n  .padding(y, medium);\n  .padding(y, big, sm);\n  background: linear-gradient(-180deg, @white 0%, @shade-200 100%);\n  .gatsby_site_article_foot + .gatsby_site_footer & {\n    background: @shade-200;\n  }\n  font-size: 12px;\n  @media @max-sm {\n    font-size: 11px;\n  }\n  a {\n    .font-sans-semibold();\n    color: @black;\n    text-decoration: none;\n    // animation\n    &:hover, &.hover, &:active, &.active {\n      color: @black;\n      text-decoration: underline;\n    }\n  }\n}\n\n.gatsby_site_footer_bottom_row {\n  .flex-auto();\n  .row-space(tiny);\n  .row-space(small, sm);\n  align-items: center;\n  justify-content: space-between;\n}\n\n.gatsby_site_footer_bottom_right {\n  @media @min-sm {\n    margin-left: auto;\n  }\n}\n"