export default "@import 'xtend-library/src/extensions/loader/_loader.less';\n\n/**\n * loader\n */\n\n// spinner\n\n.spinner {\n  width: 25px;\n  height: 25px;\n  .btn & {\n    width: 1.25em;\n    height: 1.25em;\n  }\n  line-height: 0;\n  svg {\n    filter: drop-shadow(0 .1rem .2rem fade(@black, 10%)) drop-shadow(0 .2rem .4rem fade(@black, 25%));\n    circle {\n      fill: none;\n      stroke-width: 30;\n      transform-origin: center center;\n      .btn & {\n        stroke-width: 40;\n      }\n    }\n    &:nth-child(1) {\n      circle {\n        stroke: @accent;\n        stroke-dashoffset: 628;\n        transform: rotateY(0deg) rotateZ(-90deg);\n        animation: anim-spinner @time-small * 2 infinite @ease-in;\n      }\n    }\n    &:nth-child(2) {\n      position: absolute;\n      top: 0;\n      left: 0;\n      circle {\n        stroke: fade(@accent, 5%);\n        stroke-dashoffset: 0;\n      }\n    }\n  }\n}\n\n@keyframes anim-spinner {\n  0% {\n    stroke-dashoffset: 628;\n    transform: rotateY(0deg) rotateZ(-90deg);\n    // fix no support transform-box: border-box;\n    transform-origin: 48% 48%;\n  }\n  50% {\n    stroke-dashoffset: 0;\n    transform: rotateY(0deg) rotateZ(90deg);\n    animation-timing-function: @ease-out;\n    // fix no support transform-box: border-box;\n    transform-origin: 48% 48%;\n  }\n  50.0001% {\n    stroke-dashoffset: 0;\n    transform: rotateY(180deg) rotateZ(90deg);\n    // fix no support transform-box: border-box;\n    transform-origin: center center;\n  }\n  100% {\n    stroke-dashoffset: 628;\n    transform: rotateY(180deg) rotateZ(-90deg);\n    // fix no support transform-box: border-box;\n    transform-origin: 46% 50%;\n  }\n}\n\n// direction\n\n.loader-x {\n  .filler {\n    span:nth-child(1) {\n      width: 0;\n      background: linear-gradient(90deg, @grad-accent);\n      box-shadow: @shadow-accent;\n      animation: anim-loader-x @time-small * 2 infinite @ease-in;\n    }\n    span:nth-child(2) {\n      background: fade(@accent, 5%);\n    }\n  }\n}\n\n.loader-y {\n  .filler {\n    span:nth-child(1) {\n      height: 0;\n      background: linear-gradient(0deg, @grad-accent);\n      box-shadow: @shadow-accent;\n      animation: anim-loader-y @time-small * 2 infinite @ease-in;\n    }\n    span:nth-child(2) {\n      background: fade(@accent, 5%);\n    }\n  }\n}\n\n@keyframes anim-loader-x {\n  0% {\n    left: 0;\n    width: 0;\n  }\n  50% {\n    left: 0;\n    width: 100%;\n    animation-timing-function: @ease-out;\n  }\n  100% {\n    left: 100%;\n    width: 0;\n  }\n}\n\n@keyframes anim-loader-y {\n  0% {\n    top: 100%;\n    height: 0;\n  }\n  50% {\n    top: 0;\n    height: 100%;\n    animation-timing-function: @ease-out;\n  }\n  100% {\n    top: 0;\n    height: 0;\n  }\n}\n\n// size\n\n.loader-size-x, .loader-size-top, .loader-size-bottom {\n  .filler {\n    height: 3px;\n  }\n}\n\n.loader-size-y, .loader-size-left, .loader-size-right {\n  .filler {\n    width: 3px;\n  }\n}\n\n.loader-size-x {\n  .filler {\n    left: 25%;\n    right: 25%;\n  }\n}\n\n.loader-size-y {\n  .filler {\n    top: 25%;\n    bottom: 25%;\n  }\n}\n\n// background\n\n.loader-background {\n  background: fade(@white, 80%);\n}\n\n// animation\n\n.loader-animated {\n  opacity: 0;\n  transition: opacity @time-tiny @ease-out;\n  .spinner, .filler {\n    opacity: 0;\n    transform: scale(.75);\n    transition: opacity @time-tiny @ease-out, transform @time-tiny @ease-out;\n  }\n  &.in {\n    opacity: 1;\n    transition: opacity @time-tiny @ease-in;\n    .spinner, .filler {\n      opacity: 1;\n      transform: scale(1);\n      transition: opacity @time-tiny @ease-in, transform @time-tiny @ease-in;\n    }\n  }\n  &.out {\n    opacity: 0;\n    transition: opacity @time-tiny @ease-out;\n    .spinner, .filler {\n      opacity: 0;\n      transform: scale(.75);\n      transition: opacity @time-tiny @ease-out, transform @time-tiny @ease-out;\n    }\n  }\n}\n\n// js\n\n.loader-js {\n  // disable animations\n  .spinner {\n    circle {\n      opacity: 1 !important;\n      .trans-anim-none() !important;\n    }\n  }\n  .filler {\n    span {\n      opacity: 1 !important;\n      .animation-none() !important;\n    }\n  }\n}\n\n// mouse-follow\n\n.loader.loader-mouse {\n  .filler, .spinner {\n    display: block;\n    position: relative;\n  }\n  // spinner\n  .loader-spinner& {\n    margin-top: -5px;\n    margin-left: 30px;\n  }\n  // x\n  .loader-x& {\n    margin-top: -5px;\n    margin-left: 40px;\n    .filler {\n      width: 60px;\n    }\n  }\n  // animation\n  .loader-animated();\n  // disable on touch\n  @media (hover: none) {\n    .loader-animated.in();\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    margin-top: -13px;\n    margin-left: -13px;\n    transform: none;\n  }\n}\n"