export default ".demo--slider-toggle-css {\n  .slides-inner {\n    overflow: hidden;\n    // dragger animations\n    .transition-none() !important;\n  }\n  .slide {\n    .toggle-block();\n  }\n  // animation\n  .slide {\n    &:after {\n      content: '';\n      position: absolute;\n      z-index: 9;\n      top: 0;\n      left: 0;\n      bottom: 0;\n      right: 0;\n      background: @white;\n      transform: translateX(0);\n    }\n    &.in {\n      .anim-x-inverse.in();\n      animation-duration: @time-small;\n      &:after {\n        animation-name: demo--slider-toggle-css-in;\n        animation-duration: @time-small;\n        animation-timing-function: @ease-in;\n        transform: translateX(-100%);\n      }\n      &.inverse {\n        .anim-x.in();\n        animation-duration: @time-small;\n        &:after {\n          animation-name: demo--slider-toggle-css-in-inverse;\n          animation-duration: @time-small;\n          animation-timing-function: @ease-in;\n          transform: translateX(100%);\n        }\n      }\n    }\n    &.out {\n      .anim-x-inverse.out();\n      animation-duration: @time-small;\n      &:after {\n        animation-name: demo--slider-toggle-css-out;\n        animation-duration: @time-small;\n        animation-timing-function: @ease-out;\n        transform: translateX(0);\n      }\n      &.inverse {\n        .anim-x.out();\n        animation-duration: @time-small;\n        &:after {\n          animation-name: demo--slider-toggle-css-out-inverse;\n          animation-duration: @time-small;\n          animation-timing-function: @ease-out;\n          transform: translateX(0);\n        }\n      }\n    }\n  }\n}\n\n@keyframes demo--slider-toggle-css-in {\n  0% {\n    transform: translateX(0);\n  }\n  100% {\n    transform: translateX(-100%);\n  }\n}\n\n@keyframes demo--slider-toggle-css-in-inverse {\n  0% {\n    transform: translateX(0);\n  }\n  100% {\n    transform: translateX(100%);\n  }\n}\n\n@keyframes demo--slider-toggle-css-out {\n  0% {\n    transform: translateX(100%);\n  }\n  100% {\n    transform: translateX(0);\n  }\n}\n\n@keyframes demo--slider-toggle-css-out-inverse {\n  0% {\n    transform: translateX(-100%);\n  }\n  100% {\n    transform: translateX(0);\n  }\n}\n"