export default "@import 'xtend-library/src/core/button/_button.less';\n\n/**\n * button\n */\n\n.btn {\n  border-radius: 6px;\n  border-width: 1px;\n  border-style: solid;\n  border-color: transparent;\n  .font-sans-semibold();\n  .line-height-tiny();\n  letter-spacing: .025em;\n  text-transform: uppercase;\n  // default options\n  .btn-medium();\n}\n\n// variant\n\n.btn-default {\n  .link-none();\n  border-color: @shade-200;\n  background: @shade-200;\n  color: @text;\n  &:hover, &.hover {\n    border-color: @shade-200-dark;\n    background: @shade-200;\n    color: @text-dark;\n  }\n  &:active, &.active {\n    border-color: @shade-200-dark;\n    background: @shade-200-dark;\n    color: @text-dark;\n  }\n}\n\n.btn-primary {\n  .link-none();\n  border-color: @accent;\n  background: @accent;\n  color: @text-inverse;\n  &:hover, &.hover {\n    border-color: @accent-dark;\n    background: @accent;\n    color: @text-inverse-dark;\n  }\n  &:active, &.active {\n    border-color: @accent-dark;\n    background: @accent-dark;\n    color: @text-inverse-dark;\n  }\n}\n\n.btn-text {\n  .link-none();\n  .btn-nodesign();\n  .btn-noeffect();\n  color: inherit;\n  &:hover, &.hover {\n    color: inherit;\n  }\n  &:active, &.active {\n    color: inherit;\n  }\n}\n\n.btn-link {\n  .btn-nodesign();\n  .btn-noeffect();\n  // reset\n  font-family: inherit;\n  font-style: inherit;\n  font-weight: inherit;\n  letter-spacing: inherit;\n  text-transform: inherit;\n}\n\n// size\n\n.btn-tiny {\n  padding: .5em 1em;\n  font-size: 10px;\n}\n\n.btn-small {\n  padding: .6em 1.2em;\n  font-size: 1.2rem;\n}\n\n.btn-medium {\n  padding: .625em 1.225em; // @FIX odd rem with .025 added\n  font-size: 1.3rem;\n}\n\n.btn-large {\n  padding: .6em 1.2em;\n  font-size: 1.4rem;\n}\n\n.btn-big {\n  padding: .625em 1.225em; // @FIX odd rem with .025 added\n  font-size: 1.5rem;\n}\n\n// special\n\n.btn-nodesign {\n  border-color: transparent;\n  background: transparent;\n  box-shadow: none;\n  &:hover, &.hover, &:active, &.active {\n    border-color: transparent;\n    background: transparent;\n    box-shadow: none;\n  }\n}\n\n.btn-noeffect {\n  .transform-none();\n  &:hover, &.hover, &:active, &.active {\n    .transform-none();\n  }\n}\n\n.btn-squared {\n  border-radius: 0;\n}\n\n.btn-noborder {\n  border: 0;\n}\n\n.btn-tall {\n  padding-top: .9em;\n  padding-bottom: .9em;\n}\n\n.btn-short {\n  padding-top: .3em;\n  padding-bottom: .3em;\n}\n\n.btn-wide {\n  padding-left: 1.8em;\n  padding-right: 1.8em;\n}\n\n.btn-narrow {\n  padding-left: .6em;\n  padding-right: .6em;\n}\n\n.btn-none {\n  .btn-nodesign();\n  .btn-squared();\n  padding: 0;\n}\n\n.btn-none-x {\n  .btn-nodesign();\n  .btn-squared();\n  padding-left: 0;\n  padding-right: 0;\n}\n\n.btn-none-y {\n  .btn-nodesign();\n  .btn-squared();\n  padding-top: 0;\n  padding-bottom: 0;\n}\n\n.btn-icon {\n  line-height: 1;\n  padding: .8em .8em;\n}\n\n// animation\n\n.btn {\n  transition: border @time-small @ease-out, background @time-small @ease-out, color @time-small @ease-out, transform @time-small @ease-out;\n  &:hover, &.hover {\n    transition: border @time-small @ease-in, background @time-small @ease-in, color @time-small @ease-in, transform @time-small @ease-in;\n  }\n  &:active, &.active {\n    transition: border @time-small @ease-in, background @time-small @ease-in, color @time-small @ease-in, transform @time-small @ease-in;\n  }\n}\n\n// custom\n\n.btn-close {\n  cursor: pointer;\n  position: absolute !important;\n  top: 1rem;\n  right: 1rem;\n  .btn-text();\n  .btn-small();\n  .btn-icon();\n  .icon-xt(@content: @icon-xt-close, @size: @icon-medium);\n}\n"