export default "\n#iframe--2019-xtend {\n//##START\n/**\n  * vars\n  */\n\n// ease\n\n@ease-in: @ease-out-quint;\n@ease-out: @ease-in-out-quint;\n\n/**\n  * button\n  */\n\n// variant\n\n.btn-default {\n  &:hover, &.hover {\n    transform: translateY(-.25rem);\n    box-shadow: @shadow-light;\n  }\n  &:active, &.active {\n    transform: none;\n    box-shadow: @shadow-none;\n  }\n}\n\n.btn-primary {\n  &:hover, &.hover {\n    transform: translateY(-.25rem);\n    box-shadow: @shadow-accent;\n  }\n  &:active, &.active {\n    transform: none;\n    box-shadow: @shadow-none;\n  }\n}\n\n// animation\n\n.btn {\n  transition: border @time-small @ease-out, background @time-small @ease-out, color @time-small @ease-out, box-shadow @time-small @ease-out, transform @time-tiny @ease-out;\n  &:hover, &.hover {\n    transition: border @time-small @ease-in, background @time-small @ease-in, color @time-small @ease-in, box-shadow @time-small @ease-in, transform @time-tiny @ease-in;\n  }\n  &:active, &.active {\n    transition: border @time-small @ease-in, background @time-small @ease-in, color @time-small @ease-in, box-shadow @time-small @ease-in, transform @time-tiny @ease-in;\n  }\n}\n\n/**\n  * drop\n  */\n\n// animation\n\n.drop-container {\n  > .drop {\n    > .drop-inner {\n      > .drop-design {\n        opacity: 0;\n        transform: scale(.75);\n      }\n      > .drop-content {\n        opacity: 0;\n        transform: translateY(-1.5rem);\n      }\n    }\n  }\n  &.in {\n    animation-duration: @time-small + @time-tiny; // needed for animation time and backdrop animation time, put max animation time\n    > .drop {\n      > .drop-inner {\n        > .drop-design {\n          opacity: 1;\n          transform: none;\n          transition: transform @time-medium @ease-in, opacity @time-medium @ease-in;\n        }\n        > .drop-content {\n          opacity: 1;\n          transform: none;\n          transition: transform @time-medium @ease-in @time-tiny, opacity @time-medium @ease-in @time-tiny;\n        }\n      }\n    }\n  }\n  &.out {\n    animation-duration: @time-small + @time-tiny; // needed for animation time and backdrop animation time, put max animation time\n    > .drop {\n      > .drop-inner {\n        > .drop-design {\n          opacity: 0;\n          transform: scale(.75);\n          transition: transform @time-small @ease-out @time-tiny, opacity @time-small @ease-out @time-tiny;\n        }\n        > .drop-content {\n          opacity: 0;\n          transform: translateY(1.5rem);\n          transition: transform @time-small @ease-out, opacity @time-small @ease-out;\n        }\n      }\n    }\n  }\n}\n\n/**\n  * overlay\n  */\n\n// animation\n\n.overlay {\n  .card-overlay {\n    > .card-design {\n      opacity: 0;\n      transform: scale(.75);\n    }\n    > .btn-close {\n      opacity: 0;\n    }\n    > .card-inner {\n      > .card-block,  > .card-content {\n        opacity: 0;\n        transform: translateY(-1.5rem);\n      }\n    }\n  }\n  &.in {\n    animation-duration: @time-medium + @time-tiny; // needed for animation time and backdrop animation time, put max animation time\n    .card-overlay {\n      > .card-design {\n        opacity: 1;\n        transform: scale(1);\n        transition: transform @time-medium @ease-in, opacity @time-medium @ease-in;\n      }\n      > .btn-close {\n        // use animation to not override .btn transitions\n        .anim-zoom.in();\n        animation-delay: @time-medium;\n      }\n      > .card-inner {\n        > .card-block,  > .card-content {\n          opacity: 1;\n          transform: none;\n          transition: transform @time-medium @ease-in @time-tiny, opacity @time-medium @ease-in @time-tiny;\n        }\n      }\n    }\n  }\n  &.out {\n    animation-duration: @time-small + @time-tiny; // needed for animation time and backdrop animation time, put max animation time\n    .card-overlay {\n      > .card-design {\n        opacity: 0;\n        transform: scale(.75);\n        transition: transform @time-small @ease-out @time-tiny, opacity @time-small @ease-out @time-tiny;\n      }\n      > .btn-close {\n        // use animation to not override .btn transitions\n        .anim-zoom.out();\n        animation-delay: initial;\n      }\n      > .card-inner {\n        > .card-block,  > .card-content {\n          opacity: 0;\n          transform: translateY(1.5rem);\n          transition: transform @time-small @ease-out, opacity @time-small @ease-out;\n        }\n      }\n    }\n  }\n}\n//##END\n}\n//##START\n/**\n  * card\n  */\n\n// variant\n\n.card-default {\n  // interactive\n  a&, label& {\n//##END\n#iframe--2019-xtend & {\n//##START\n    &:hover, &.hover {\n      > .card-design {\n        box-shadow: @shadow-light;\n      }\n    }\n    &:active, &.active {\n      > .card-design {\n        box-shadow: none;\n      }\n    }\n//##END\n}\n//##START\n  }\n}\n\n.card-primary {\n  // interactive\n  a&, label& {\n//##END\n#iframe--2019-xtend & {\n//##START\n    &:hover, &.hover {\n      > .card-design {\n        box-shadow: @shadow-accent;\n      }\n    }\n    &:active, &.active {\n      > .card-design {\n        box-shadow: none;\n      }\n    }\n//##END\n}\n//##START\n  }\n}\n\n// animation\n\n.card {\n  // interactive\n  a&, label& {\n//##END\n#iframe--2019-xtend & {\n//##START\n    transition: all @time-small @ease-out;\n    > .card-design {\n      transition: all @time-small @ease-out;\n    }\n    &:hover, &.hover {\n      transform: translateY(-.5rem);\n      transition: all @time-small @ease-in;\n      > .card-design {\n        transition: all @time-small @ease-in;\n      }\n    }\n    &:active, &.active, input[type=\"checkbox\"]:not(.unstyled):checked ~ &, input[type=\"radio\"]:not(.unstyled):checked ~ & {\n      transform: none;\n      transition: all @time-small @ease-in;\n      > .card-design {\n        transition: all @time-small @ease-in;\n      }\n    }\n//##END\n}\n//##START\n  }\n}\n//##END\n\n"