export default "/**\n * structure\n */\n\n// indexes\n\n.gatsby_site_header {\n  position: relative;\n  z-index: 9;\n}\n\n.gatsby_site_main {\n  position: relative;\n  z-index: 8;\n}\n\n.gatsby_site_footer {\n  position: relative;\n  z-index: 7;\n}\n\n// prevent horizontal overflow\n\n.gatsby_site_wrapper {\n  overflow: hidden;\n}\n\n.gatsby_site_header, .gatsby_site_main, .gatsby_site_footer {\n  .full();\n}\n\n// sticky footer\n\n.gatsby_site_wrapper {\n  display: flex;\n  flex-wrap: wrap;\n  flex-direction: column;\n  min-height: 100vh;\n}\n\n.gatsby_site_main,\n.gatsby_site_main_inner,\n.gatsby_site_article,\n.gatsby_site_article_inner {\n  display: flex;\n  flex-direction: column;\n  .flex-auto();\n}\n\n/**\n * article\n */\n\n.gatsby_site_article {\n  background: @white;\n  box-shadow: @shadow-light;\n  @media @min-md {\n    margin-left: @gatsby_site_article_sidebar_width;\n  }\n}\n\n.gatsby_site_article_hero {\n  position: relative;\n  .container-space();\n  .padding(y, giant);\n  .padding(y, huge, lg);\n  background: linear-gradient(180deg, @shade-200 0%, @white 66%);\n  p {\n    .font-big();\n    color: @shade-500;\n  }\n  // animation\n  .gatsby_site_article_hero_links_inner {\n    > .row > * {\n      opacity: 0;\n      transform: translateY(1.5rem);\n      transition: opacity @time-small @ease-in, transform @time-small @ease-in;\n      transition-delay: 150ms; // @start + @child * @increment\n      .nth-child(@child: 3, @start: 0ms, @increment: 50ms, @decrement: 0ms, {\n        @transition-delay();\n      });\n    }\n  }\n  &:hover, &:focus-within {\n    .gatsby_site_article_hero_links_inner {\n      > .row > * {\n        opacity: 1;\n        transform: translateY(0);\n        transition: opacity @time-small @ease-in, transform @time-small @ease-in;\n      }\n    }\n  }\n}\n\n.gatsby_site_article_hero_content {\n  pointer-events: none;\n  .badge {\n    margin-top: -1rem;\n    margin-left: 1.5rem;\n  }\n  h1 {\n    em {\n      .font-sans-regular();\n      color: darken(@shade-200, 10%);\n    }\n  }\n}\n\n.gatsby_site_article_hero_links {\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  .container-space();\n  .padding(y, small);\n  .row {\n    .row-space(medium);\n    justify-content: space-between;\n  }\n  .btn {\n    .link-none();\n    .btn-text();\n    .btn-small();\n    .btn-none-x();\n  }\n}\n\n.gatsby_site_article_content {\n  .flex-auto();\n  .container-space();\n  .padding(y, giant);\n  .gatsby_site_article_hero + & {\n    .padding(top, none);\n  }\n}\n\n\n.gatsby_site_article_hero_content,\n.gatsby_site_article_hero_links_inner,\n.gatsby_site_article_content_inner,\n.gatsby_site_article_foot_inner,\n.gatsby_site_footer_inner {\n  margin: auto;\n  max-width: 1200px;\n}\n\n.gatsby_site_article_foot {\n  .container-space();\n  .padding(y, big);\n  background: linear-gradient(-180deg, @white 33%, @shade-200 100%);\n}\n\n/**\n * listing\n */\n\n.gatsby_listing {\n  > .row {\n    .row-space(large);\n  }\n}\n\n.gatsby_listing_group {\n  .col(12);\n}\n\n.gatsby_listing_title {\n  .h5();\n  .header-block();\n  .spacing-large();\n}\n\n.gatsby_listing_items {\n  > .row {\n    .row-space(large);\n    .row-stretch();\n  }\n}\n\n.gatsby_listing_column {\n  .col(12);\n  .col(6, sm);\n  .col(4, lg);\n}\n\n.gatsby_listing_item {\n  // interactive\n  a&, label& {\n    &:hover, &.hover {\n      > .card-design {\n        box-shadow: @shadow-accent;\n      }\n    }\n    &:active, &.active {\n      > .card-design {\n        box-shadow: none;\n      }\n    }\n  }\n  // animation\n  a&, label& {\n    transition: all @time-small @ease-out;\n    > .card-design {\n      transition: all @time-small @ease-out;\n    }\n    &:hover, &.hover {\n      transform: translateY(-.5rem);\n      transition: all @time-small @ease-in;\n      > .card-design {\n        transition: all @time-small @ease-in;\n      }\n    }\n    &:active, &.active, input[type=\"checkbox\"]:not(.unstyled):checked ~ &, input[type=\"radio\"]:not(.unstyled):checked ~ & {\n      transform: none;\n      transition: all @time-small @ease-in;\n      > .card-design {\n        transition: all @time-small @ease-in;\n      }\n    }\n  }\n}\n"