export default "/**\n * slider-navigation-sides\n */\n\n.slider-navigation-sides {\n  cursor: none;\n  position: absolute;\n  z-index: @index-top;\n  top: 0;\n  width: 5rem;\n  height: 100%;\n  color: @black;\n  // disable\n  .slider.xt-disable & {\n    display: none;\n  }\n  // not touch\n  @media (hover: hover) {\n    width: 10rem;\n    opacity: 0;\n    &.in {\n      opacity: 1;\n    }\n    &.out {\n      opacity: 0;\n    }\n  }\n  // animation\n  .slider-navigation-sides-btn {\n    transition: opacity @time-small @ease-out, transform @time-small @ease-out;\n  }\n  &:active {\n    .slider-navigation-sides-btn {\n      opacity: .75;\n      transform: scale(0.8);\n      transition: opacity @time-small @ease-in, transform @time-small @ease-in;\n    }\n  }\n}\n\n.slider-navigation-sides-prev {\n  left: 0;\n}\n\n.slider-navigation-sides-next {\n  right: 0;\n}\n\n.slider-navigation-sides-btn {\n  padding: 0;\n  line-height: 0;\n  font-size: 2rem;\n  // not touch\n  @media (hover: hover) {\n    .mouse-follow();\n  }\n  // touch\n  @media (hover: none) {\n    position: absolute;\n    top: 50% !important;\n    left: 50% !important;\n    margin-top: -1rem;\n    margin-left: -1rem;\n    .transform-none() !important;\n  }\n}\n"