export default "@import 'xtend-library/src/extensions/slider/_slider.less';\n@import 'xtend-library/src/core/javascript/javascript.less';\n\n/**\n * slider\n */\n\n// slides\n\n.slides {\n  .full();\n  &.xt-autoHeight {\n    // autoHeight animations\n    transition: height 750ms @ease-out;\n  }\n}\n\n.slides-inner {\n  .row();\n  .row-space(small);\n  flex-wrap: nowrap;\n  align-items: flex-start;\n  // dragger animations\n  transition: transform 750ms @ease-out-quint;\n}\n\n// pagination\n\n.slider-pagination {\n  .list();\n  .list-space(small);\n  align-items: center;\n  justify-content: center;\n  .padding(top, small);\n}\n\n[data-xt-pag].btn {\n  .btn-tiny();\n  border-radius: 2rem;\n  padding: .5em 1.15em;\n  min-height: 2.2rem;\n  &.active {\n    padding: .5em 2em;\n  }\n}\n\n// navigation\n\n[data-xt-nav].btn {\n  .btn-tiny();\n  padding: .5em 1.15em;\n  min-height: 2.2rem;\n  border-radius: 2rem;\n  [class^='icon-'], [class*=' icon-'] {\n    .icon-small();\n  }\n}\n\n// other\n\n.slider-touch-overflow {\n  @media (hover: none) {\n    .xt-disable();\n    .slides {\n      overflow-x: auto;\n      overflow-y: hidden;\n      .overflow-style(@mode: inner);\n    }\n    .slides-inner {\n      cursor: auto !important; // no grab cursor\n      .transition-none() !important;\n      .transform-none() !important;\n    }\n    .slider-pagination {\n      display: none !important;\n    }\n  }\n}\n"