export default "#iframe--2019-position-slide {\n//##START\n/**\n  * vars\n  */\n\n// ease\n\n@ease-in: @ease-out-quint;\n@ease-out: @ease-in-out-quint;\n\n/**\n  * overlay\n  */\n\n// animation\n\n.overlay-position {\n  &.overlay-position-left {\n    .overlay-design {\n      opacity: 0;\n      transform: translateX(-100%);\n    }\n    .card-overlay {\n      > .card-inner {\n        > .card-block,  > .card-content {\n          opacity: 0;\n          transform: translateX(-125%);\n        }\n      }\n    }\n  }\n  &.overlay-position-right {\n    .overlay-design {\n      opacity: 0;\n      transform: translateX(100%);\n    }\n    .card-overlay {\n      > .card-inner {\n        > .card-block,  > .card-content {\n          opacity: 0;\n          transform: translateX(125%);\n        }\n      }\n    }\n  }\n  &.in {\n    animation-duration: @time-medium; // needed for animation time and backdrop animation time, put max animation time\n    &.overlay-position {\n      .overlay-design {\n        opacity: 1;\n        transform: none;\n        transition: transform @time-medium @ease-in, opacity @time-tiny @ease-in;\n      }\n      .card-overlay {\n        > .card-inner {\n          > .card-block,  > .card-content {\n            opacity: 1;\n            transform: none;\n            transition: transform @time-medium @ease-in, opacity @time-tiny @ease-in;\n          }\n        }\n      }\n    }\n  }\n  &.out {\n    animation-duration: @time-small; // needed for animation time and backdrop animation time, put max animation time\n    &.overlay-position-left {\n      .overlay-design {\n        opacity: 0;\n        transform: translateX(-100%);\n        transition: transform @time-small @ease-out, opacity @time-tiny @ease-out @time-tiny;\n      }\n      .card-overlay {\n        > .card-inner {\n          > .card-block,  > .card-content {\n            opacity: 0;\n            transform: translateX(-125%);\n            transition: transform @time-small @ease-out, opacity @time-tiny @ease-out @time-tiny;\n          }\n        }\n        > .btn-close {\n          .anim-x-inverse.out();\n        }\n      }\n    }\n    &.overlay-position-right {\n      .overlay-design {\n        opacity: 0;\n        transform: translateX(100%);\n        transition: transform @time-small @ease-out, opacity @time-tiny @ease-out @time-tiny;\n      }\n      .card-overlay {\n        > .card-inner {\n          > .card-block,  > .card-content {\n            opacity: 0;\n            transform: translateX(125%);\n            transition: transform @time-small @ease-out, opacity @time-tiny @ease-out @time-tiny;\n          }\n        }\n        > .btn-close {\n          .anim-x.out();\n        }\n      }\n    }\n  }\n}\n//##END\n}\n"