export default "@import 'xtend-library/src/core/animation/_animation.less';\n\n/**\n * vars\n */\n\n// time\n\n@time-micro: 50ms;\n@time-mini: 100ms;\n@time-tiny: 250ms;\n@time-small: 500ms;\n@time-medium: 750ms;\n@time-large: 1000ms;\n@time-big: 2000ms;\n@time-giant: 3000ms;\n@time-huge: 4000ms;\n\n// ease\n\n@ease-cheetah: ~'cubic-bezier(0.14, 0.63, 0, 1)';\n@ease-tiger: ~'cubic-bezier(0.96, 0.1, 0.14, 0.88)';\n@ease-cat: ~'cubic-bezier(0.77, 0, 0.17, 1)';\n@ease-dog: ~'cubic-bezier(0.36, 0, 0, 1)';\n@ease-penguin: ~'cubic-bezier(0.68, 0.13, 0.25, 1)';\n@ease-in: @ease-out-quad;\n@ease-out: @ease-in-out-quad;\n\n@ease-in-sine: cubic-bezier(0.470, 0.000, 0.745, 0.715);\n@ease-out-sine: cubic-bezier(0.390, 0.575, 0.565, 1.000);\n@ease-in-out-sine: cubic-bezier(0.445, 0.050, 0.550, 0.950);\n\n@ease-in-quad: cubic-bezier(0.550, 0.085, 0.680, 0.530);\n@ease-out-quad: cubic-bezier(0.250, 0.460, 0.450, 0.940);\n@ease-in-out-quad: cubic-bezier(0.455, 0.030, 0.515, 0.955);\n\n@ease-in-cubic: cubic-bezier(0.550, 0.055, 0.675, 0.190);\n@ease-out-cubic: cubic-bezier(0.215, 0.610, 0.355, 1.000);\n@ease-in-out-cubic: cubic-bezier(0.645, 0.045, 0.355, 1.000);\n\n@ease-in-quart: cubic-bezier(0.895, 0.030, 0.685, 0.220);\n@ease-out-quart: cubic-bezier(0.165, 0.840, 0.440, 1.000);\n@ease-in-out-quart: cubic-bezier(0.770, 0.000, 0.175, 1.000);\n\n@ease-in-quint: cubic-bezier(0.755, 0.050, 0.855, 0.060);\n@ease-out-quint: cubic-bezier(0.230, 1.000, 0.320, 1.000);\n@ease-in-out-quint: cubic-bezier(0.860, 0.000, 0.070, 1.000);\n\n@ease-in-expo: cubic-bezier(0.950, 0.050, 0.795, 0.035);\n@ease-out-expo: cubic-bezier(0.190, 1.000, 0.220, 1.000);\n@ease-in-out-expo: cubic-bezier(1.000, 0.000, 0.000, 1.000);\n\n@ease-in-circ: cubic-bezier(0.600, 0.040, 0.980, 0.335);\n@ease-out-circ: cubic-bezier(0.075, 0.820, 0.165, 1.000);\n@ease-in-out-circ: cubic-bezier(0.785, 0.135, 0.150, 0.860);\n\n@ease-in-back: cubic-bezier(0.600, -0.280, 0.735, 0.045);\n@ease-out-back: cubic-bezier(0.175, 0.885, 0.320, 1.275);\n@ease-in-out-back: cubic-bezier(0.680, -0.550, 0.265, 1.550);\n\n/**\n * animation\n */\n\n// anim-y\n\n.anim-y {\n  &.in {\n    opacity: 0;\n    animation-name: anim-y-in;\n    animation-duration: @time-small;\n    animation-timing-function: @ease-in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-y-out;\n    animation-duration: @time-small;\n    animation-timing-function: @ease-out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-y-in {\n  0% {\n    opacity: 0;\n    transform: translateY(-1.5rem);\n  }\n  100% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n@keyframes anim-y-out {\n  0% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n  100% {\n    opacity: 0;\n    transform: translateY(1.5rem);\n  }\n}\n\n// anim-y-inverse\n\n.anim-y-inverse {\n  &.in {\n    opacity: 0;\n    animation-name: anim-y-inverse-in;\n    animation-duration: @time-small;\n    animation-timing-function: @ease-in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-y-inverse-out;\n    animation-duration: @time-small;\n    animation-timing-function: @ease-out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-y-inverse-in {\n  0% {\n    opacity: 0;\n    transform: translateY(1.5rem);\n  }\n  100% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n@keyframes anim-y-inverse-out {\n  0% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n  100% {\n    opacity: 0;\n    transform: translateY(-1.5rem);\n  }\n}\n\n// anim-x\n\n.anim-x {\n  &.in {\n    opacity: 0;\n    animation-name: anim-x-in;\n    animation-duration: @time-small;\n    animation-timing-function: @ease-in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-x-out;\n    animation-duration: @time-small;\n    animation-timing-function: @ease-out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-x-in {\n  0% {\n    opacity: 0;\n    transform: translateX(-1.5rem);\n  }\n  100% {\n    opacity: 1;\n    transform: translateX(0);\n  }\n}\n\n@keyframes anim-x-out {\n  0% {\n    opacity: 1;\n    transform: translateX(0);\n  }\n  100% {\n    opacity: 0;\n    transform: translateX(1.5rem);\n  }\n}\n\n// anim-x-inverse\n\n.anim-x-inverse {\n  &.in {\n    opacity: 0;\n    animation-name: anim-x-inverse-in;\n    animation-duration: @time-small;\n    animation-timing-function: @ease-in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-x-inverse-out;\n    animation-duration: @time-small;\n    animation-timing-function: @ease-out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-x-inverse-in {\n  0% {\n    opacity: 0;\n    transform: translateX(1.5rem);\n  }\n  100% {\n    opacity: 1;\n    transform: translateX(0);\n  }\n}\n\n@keyframes anim-x-inverse-out {\n  0% {\n    opacity: 1;\n    transform: translateX(0);\n  }\n  100% {\n    opacity: 0;\n    transform: translateX(-1.5rem);\n  }\n}\n\n// anim-zoom\n\n.anim-zoom {\n  &.in {\n    opacity: 0;\n    animation-name: anim-zoom-in;\n    animation-duration: @time-small;\n    animation-timing-function: @ease-in;\n    animation-fill-mode: forwards;\n  }\n  &.out {\n    opacity: 1;\n    animation-name: anim-zoom-out;\n    animation-duration: @time-small;\n    animation-timing-function: @ease-out;\n    animation-fill-mode: forwards;\n  }\n}\n\n@keyframes anim-zoom-in {\n  0% {\n    opacity: 0;\n    transform: scale(1.2);\n  }\n  100% {\n    opacity: 1;\n    transform: scale(1);\n  }\n}\n\n@keyframes anim-zoom-out {\n  0% {\n    opacity: 1;\n    transform: scale(1);\n  }\n  100% {\n    opacity: 0;\n    transform: scale(1.2);\n  }\n}\n\n/**\n * custom\n */\n\n// collapse\n\n.collapse-height {\n  &.in {\n    transition: height @time-medium @ease-out;\n  }\n  &.out {\n    transition: height @time-medium @ease-out;\n  }\n}\n\n.collapse-width {\n  &.in {\n    transition: width @time-medium @ease-out;\n  }\n  &.out {\n    transition: width @time-medium @ease-out;\n  }\n}\n\n/**\n * prefers-reduced-motion\n */\n\n@media (prefers-reduced-motion: reduce), (update: slow) {\n  *, *:before, *:after {\n    transition-duration: initial !important;\n    transition-delay: initial !important;\n    animation-duration: initial !important;\n    animation-delay: initial !important;\n    animation-iteration-count: 1 !important;\n  }\n}\n"