export default "@import 'xtend-library/src/core/group/_group.less';\n\n/**\n * group\n */\n\n// variant\n\n.group-default {\n  @group-border-width: 2px;\n  @group-border-radius: 6px;\n  border-radius: @group-border-radius;\n  border-width: @group-border-width;\n  border-style: solid;\n  border-color: transparent;\n  // content\n  .group-inner {\n    > .btn {\n      border-radius: @group-border-radius;\n      border-width: @group-border-width;\n      border-color: @shade-200 !important;\n      &:hover, &.hover, &:active, &.active {\n        border-color: @shade-200-dark !important;\n      }\n    }\n    > .form-item {\n      border-radius: @group-border-radius;\n      border-width: @group-border-width;\n      border-color: @shade-200 !important;\n      &:focus, &.active {\n        border-color: @shade-200-dark !important;\n      }\n    }\n  }\n  // collapse border\n  .group-inner {\n    &:not(:last-child) {\n      margin-right: -@group-border-width;\n    }\n    > * + * {\n      margin-top: -@group-border-width;\n    }\n  }\n  // list\n  > .list {\n    .list-space(small) !important;\n    flex-wrap: nowrap;\n  }\n}\n"