export default "import { Xt } from 'xtend-library/src/xt.js'\n\nXt.mount.push({\n  matches: '#overlay-nested-toggle',\n  mount: object => {\n    // event\n\n    const click = () => {\n      document.querySelector('#overlay-nested').dispatchEvent(new CustomEvent('on.trigger.xt'))\n    }\n\n    object.addEventListener('click', click)\n  },\n})\n"