export default "/**\n * reset\n */\n\nhtml, body {\n  height: 100%;\n  touch-action: manipulation; // disable double-tap\n}\n\nhtml {\n  box-sizing: border-box;\n  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); // disable tap highlight\n}\n\nbody {\n  margin: 0;\n}\n\n*, *:before, *:after {\n  -webkit-box-sizing: inherit;\n  box-sizing: inherit;\n}\n\na img {\n  // ie10 remove border\n  border: 0;\n  outline: 0;\n}\n\na, button {\n  cursor: pointer;\n  text-decoration: none;\n  // enables pointer events\n  display: inline-block;\n}\n\nbutton {\n  .reset-text();\n  border: 0;\n  padding: 0;\n  background: transparent;\n  text-align: left;\n}\n\nimg {\n  display: inline-block;\n  vertical-align: top; // fix inline-block spacing\n}\n\nsvg {\n  display: block;\n  text-anchor: middle;\n}\n\nh1, h2, h3, h4, h5, h6, p {\n  margin: 0;\n}\n\naddress {\n  font-style: inherit;\n}\n\ndl, dd {\n  margin: 0;\n}\n\nfieldset {\n  border: 0;\n  margin: 0;\n  padding: 0;\n  min-inline-size: auto;\n}\n\nlegend {\n  border: 0;\n  margin: 0;\n  padding: 0;\n}\n\nlabel[for] {\n  cursor: pointer;\n}\n\ninput:not([type=\"checkbox\"]):not([type=\"radio\"]), textarea {\n  // remove shadow\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n}\n\nselect {\n  cursor: pointer;\n  // remove arrow\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n  &::-ms-expand {\n    display: none;\n  }\n}\n\nbutton, input, select, textarea {\n  // safari margin\n  margin: 0;\n}\n\ninput, select, textarea {\n  // autofill\n  &:-webkit-autofill, &:-webkit-autofill:hover, &:-webkit-autofill:focus {\n    transition-property: background-color, color;\n    transition-delay: 10000000s;\n  }\n}\n\nhtml:not(.xt-focus-visible), html.xt-focus-disable {\n  :focus {\n    outline: none;\n  }\n}\n"