export default ".demo--scroll-container--responsive > * {\n  @media @max-sm {\n    // scroll\n    &:not(.scroll-done) {\n      opacity: 0;\n      .transition-none() !important; // fix animation on links\n      &.fade {\n        .anim-x.in();\n      }\n      &.fade-out {\n        .anim-x.out();\n      }\n    }\n  }\n  @media @min-sm {\n    // infinite\n    opacity: 0;\n    .transition-none() !important; // fix animation on links\n    &.fade {\n      .anim-x.in();\n    }\n    &.fade-out {\n      .anim-x.out();\n    }\n  }\n}\n"