export default "@import 'xtend-library/src/core/global/_global.less';\n\n/**\n * vars\n */\n\n// generate class: [false], [true] non responsive only, [sm, md, lg] responsive @breakpoints\n\n@generate-util: sm, md, lg;\n@generate-flex: true;\n@generate-margin: true;\n@generate-padding: true;\n\n// colors\n// USAGE: background: @white;\n\n@white: #fff;\n@black: #000;\n\n@shade-200: #eff4fd;\n@shade-200-dark: darken(@shade-200, 5%);\n@shade-500: #7a7e8e;\n@shade-900: #021a46;\n\n@accent: #0078ff;\n@accent-dark: darken(@accent, 5%);\n\n@text: @shade-900;\n@text-dark: @black;\n@text-inverse: @white;\n@text-inverse-dark: fade(@white, 90%);\n\n@success: #179922;\n@error: #bc1d1f;\n\n// shadow\n// USAGE: box-shadow: @shadow-light;\n\n@shadow-none: 0 .25rem .5rem transparent, 0 .75rem 1.5rem transparent;\n@shadow-light: 0 .25rem .5rem fade(@shade-500, 5%), 0 .75rem 1.5rem fade(@shade-500, 10%);\n@shadow-dark: 0 .25rem .5rem fade(@shade-900, 20%), 0 .75rem 1.5rem fade(@shade-900, 40%);\n@shadow-accent: 0 .25rem .5rem fade(@accent-dark, 20%), 0 .75rem 1.5rem fade(@accent-dark, 40%);\n\n// gradient\n// USAGE: background: linear-gradient(135deg, @grad-light);\n\n@grad-light: @shade-200 33%, darken(@shade-200, 2%) 100%;\n@grad-dark: @shade-900 33%, darken(@shade-900, 2%) 100%;\n@grad-accent: @accent 33%, @accent-dark 100%;\n\n// spacing\n// USAGE: .margin(x, small); .margin(y, small); .margin(all, small); .margin-remove(all, small);\n// USAGE: .padding(x, small); .padding(y, small); .padding(all, small);\n\n@margins: auto auto, none 0, micro .25rem, mini .5rem, tiny 1rem, small 1.5rem, medium 2rem, large 3rem, big 4rem, giant 6rem, huge 8rem;\n@paddings: auto auto, none 0, micro .25rem, mini .5rem, tiny 1rem, small 1.5rem, medium 2rem, large 3rem, big 4rem, giant 6rem, huge 8rem;\n\n// order\n// USAGE: .order(1); // eval to -2\n\n@orders: -3, -2, -1, 0, 1, 2, 3;\n\n// z-index\n// USAGE: z-index: @index-base;\n\n@index-base: 0;\n@index-active: 2;\n@index-top: 40;\n@index-btn-close: 50;\n@index-sticky: 500; // @index-sticky decreses with sequential sticky\n@index-top: 501;\n@index-indicator: 510;\n@index-backdrop: 530;\n@index-drop: 600;\n@index-overlay: 700;\n@index-last: 701;\n\n// breakpoints\n\n@breakpoints: xxs, xs, sm, md, lg, xl; // if you change this change also variables names below\n@breakpoint-xxs: 370px;\n@breakpoint-xs: 576px;\n@breakpoint-sm: 768px;\n@breakpoint-md: 992px;\n@breakpoint-lg: 1200px;\n@breakpoint-xl: 1600px;\n\n// @media queries\n// USAGE: @media @min-sm and @max-md {}\n\n@min-xxs: ~'(min-width:'(@breakpoint-xxs)~')';\n@max-xxs: ~'(max-width:'(@breakpoint-xxs - 1)~')';\n@min-xs: ~'(min-width:'(@breakpoint-xs)~')';\n@max-xs: ~'(max-width:'(@breakpoint-xs - 1)~')';\n@min-sm: ~'(min-width:'(@breakpoint-sm)~')';\n@max-sm: ~'(max-width:'(@breakpoint-sm - 1)~')';\n@min-md: ~'(min-width:'(@breakpoint-md)~')';\n@max-md: ~'(max-width:'(@breakpoint-md - 1)~')';\n@min-lg: ~'(min-width:'(@breakpoint-lg)~')';\n@max-lg: ~'(max-width:'(@breakpoint-lg - 1)~')';\n@min-xl: ~'(min-width:'(@breakpoint-xl)~')';\n@max-xl: ~'(max-width:'(@breakpoint-xl - 1)~')';\n\n/**\n * background\n */\n\nbody {\n  background: @shade-200;\n}\n\n/**\n * scrollbar\n */\n\n.overflow-style(@mode: none) {\n  -webkit-overflow-scrolling: touch;\n  & when (@mode = none) {\n    &::-webkit-scrollbar {\n      height: 8px;\n      width: 8px;\n      background: @shade-200;\n      @media (hover: none) {\n        display: none;\n      }\n    }\n    &::-webkit-scrollbar-thumb {\n      background: fade(@shade-900, 50%);\n    }\n  }\n  & when (@mode = inner) {\n    &::-webkit-scrollbar {\n      height: 4px;\n      width: 4px;\n      background: transparent;\n    }\n    &::-webkit-scrollbar-thumb {\n      background: fade(@shade-900, 50%);\n    }\n  }\n}\n\n/**\n * selection\n */\n\n::selection {\n  background-color: @accent-dark !important;\n  color: @white !important;\n}\n\n/**\n * focus\n * to disable focus add html.xt-focus-disable class\n */\n\nhtml.xt-focus-visible:not(.xt-focus-disable) {\n  :focus {\n    outline-color: rgb(77, 144, 254);\n  }\n  // custom focus on forms\n  input:not([type=\"checkbox\"]):not([type=\"radio\"]), select, textarea {\n    &:focus {\n      outline: none;\n    }\n  }\n  /*\n  // custom focus for checkbox and radio\n  input[type=\"checkbox\"], input[type=\"radio\"] {\n    &:focus, &:focus ~ .form-label:before {\n      outline-color: rgb(77, 144, 254);\n      outline-offset: -2px;\n      outline-style: auto;\n      outline-width: 5px;\n    }\n  }\n  */\n}\n\n/**\n * disabled\n */\n\n[disabled], .disabled {\n  cursor: not-allowed;\n  pointer-events: none;\n  // when not .active also graphic changes\n  &:not(.active) {\n    opacity: .75;\n  }\n}\n"