export default "import { Xt } from 'xtend-library/src/xt.js'\nimport 'xtend-library/src/addons/form/textarea-autosize.js'\n\nXt.mount.push({\n  matches: '.demo--textarea-autosize',\n  mount: object => {\n    // init\n\n    let self = new Xt.TextareaAutosize(object, {})\n\n    // unmount\n\n    const unmount = () => {\n      self.destroy()\n      self = null\n    }\n    return unmount\n  },\n})\n"