export default "/**\n * loader\n */\n\n.loader {\n  position: absolute;\n  z-index: @index-top;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  border-radius: inherit;\n  overflow: hidden;\n}\n\n// filler\n\n.filler {\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  margin: auto;\n  span {\n    position: absolute;\n    top: 0;\n    left: 0;\n  }\n}\n\n// spinner\n\n.spinner {\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  margin: auto;\n}\n\n// direction\n\n.loader-x {\n  .filler {\n    span {\n      width: 100%;\n      height: 100%;\n    }\n  }\n}\n\n.loader-y {\n  .filler {\n    span {\n      width: 100%;\n      height: 100%;\n    }\n  }\n}\n\n// size\n\n.loader-size-top {\n  .filler {\n    bottom: auto;\n    span {\n      top: 0;\n    }\n  }\n}\n\n.loader-size-bottom {\n  .filler {\n    top: auto;\n    span {\n      bottom: 0;\n    }\n  }\n}\n\n.loader-size-left {\n  .filler {\n    right: auto;\n    span {\n      left: 0;\n    }\n  }\n}\n\n.loader-size-right {\n  .filler {\n    left: auto;\n    span {\n      right: 0;\n    }\n  }\n}\n"