export default "#iframe--2020-brands-infinite {\n//##START\n/**\n  * vars\n  */\n\n// ease\n\n@ease-in: @ease-out-quint;\n@ease-out: @ease-out-quint;\n\n/**\n  * slider\n  */\n.slider {\n  .padding(y, medium);\n  .slides-inner {\n    // dragger animations\n    .transition-none() !important;\n  }\n  // .slide_item\n  .slide_item {\n    .list();\n    flex-wrap: nowrap;\n  }\n  .slide_content {\n    .link-none();\n  }\n  // nooverflow\n  &.slider-nooverflow {\n    .slides-inner {\n      justify-content: center;\n    }\n  }\n}\n\n// .slider--img\n\n.slider--img {\n  .slides-inner {\n    .row-space(medium); // same as .slide_item\n  }\n  .slide_item {\n    .list-space(medium); // same as .slides-inner\n  }\n  .slide_content {\n    .col-custom(300px);\n    // animation\n    transition: transform @time-small @ease-out;\n    &:hover, &.hover, &:active, &.active {\n      transform: scale(1.05);\n      transition: transform @time-small @ease-in;\n    }\n  }\n}\n\n// .slider--text\n\n.slider--text {\n  .slides-inner {\n    .row-space(giant); // same as .slide_item\n  }\n  .slide_item {\n    .list-space(giant); // same as .slides-inner\n  }\n  .slide_content {\n    .font-sans-semibold();\n    font-size: 12px;\n    color: @text !important;\n    text-transform: uppercase;\n    white-space: nowrap;\n    @media @min-sm {\n      font-size: 1.6rem;\n    }\n    // animation\n    transition: color @time-small @ease-out;\n    &:hover, &.hover, &:active, &.active {\n      color: @accent !important;\n      transition: color @time-small @ease-in;\n    }\n  }\n  // gradient on left and right\n  &:before {\n    content: '';\n    position: absolute;\n    z-index: 2;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    width: 4rem;\n    background: linear-gradient(90deg, @white 0%, fade(@white, 0%) 100%);\n  }\n  &:after {\n    content: '';\n    position: absolute;\n    z-index: 2;\n    top: 0;\n    bottom: 0;\n    right: 0;\n    width: 4rem;\n    background: linear-gradient(90deg, fade(@white, 0%) 0%, @white 100%);\n  }\n}\n//##END\n}\n"