export default "@import 'xtend-library/src/core/drop/_drop.less';\n@import 'xtend-library/src/core/list/list.less';\n@import 'xtend-library/src/core/card/card.less';\n\n/**\n * vars\n */\n\n// space between button and drop\n\n@drop-space: 6px;\n\n/**\n * drop\n */\n\n.drop {\n  > .drop-inner {\n    border-radius: 6px;\n    > .drop-content {\n      > .list, > .list-block {\n        .padding(y, 1.3rem); // vertical space\n      }\n    }\n    > .drop-design, > .drop-content {\n      border-width: 1px;\n      border-style: solid;\n      border-color: transparent;\n    }\n  }\n  // default options\n  .drop-left();\n  .drop-bottom();\n  .drop-size-small();\n  .drop-small();\n}\n\n// variant\n\n.drop-default {\n  > .drop-inner {\n    .text-default();\n    > .drop-design {\n      border-color: @shade-200;\n      background: @white;\n      box-shadow: @shadow-light;\n    }\n  }\n  > .drop-inner > .drop-content > .list-drop {\n    .list-btns({ .btn-text() });\n  }\n}\n\n.drop-primary {\n  > .drop-inner {\n    .text-inverse();\n    > .drop-design {\n      border-color: @accent-dark;\n      background: @accent;\n      box-shadow: @shadow-dark;\n    }\n  }\n  > .drop-inner > .drop-content > .list-drop {\n    .list-btns({ .btn-text() });\n  }\n}\n\n// size\n\n.drop-size-small {\n  .list-drop {\n    max-width: 25rem;\n  }\n  .card-drop {\n    max-width: 35rem;\n  }\n}\n\n.drop-size-medium {\n  .list-drop {\n    max-width: 35rem;\n  }\n  .card-drop {\n    max-width: 45rem;\n  }\n}\n\n.drop-size-large {\n  .list-drop {\n    max-width: 45rem;\n  }\n  .card-drop {\n    max-width: 55rem;\n  }\n}\n\n// content size\n\n.drop-small {\n  // list\n  .list-drop {\n    .list-btns({\n      .btn-small();\n      .padding(x, 2.5rem);\n    });\n  }\n  // card\n  .card-drop {\n    .card-small();\n    .card-block {\n      padding: 2em 2em !important;\n    }\n  }\n}\n\n.drop-medium {\n  // list\n  .list-drop {\n    .list-btns({\n      .btn-medium();\n      .padding(x, 3rem);\n    });\n  }\n  // card\n  .card-drop {\n    .card-small();\n    .card-block {\n      padding: 2em 2em !important;\n    }\n    @media @min-sm {\n      .card-medium();\n      .card-block {\n        padding: 2.5em 2.5em !important;\n      }\n    }\n  }\n}\n\n.drop-large {\n  // list\n  .list-drop {\n    .list-btns({\n      .btn-large();\n      .padding(x, 3.5rem);\n    });\n  }\n  // card\n  .card-drop {\n    .card-small();\n    .card-block {\n      padding: 2em 2em !important;\n    }\n    @media @min-sm {\n      .card-large();\n      .card-block {\n        padding: 3em 3em !important;\n      }\n    }\n  }\n}\n\n// special\n\n.drop-squared {\n  > .drop-inner {\n    border-radius: 0;\n  }\n}\n\n.drop-noborder {\n  > .drop-inner {\n    > .drop-design {\n      border: 0;\n    }\n  }\n}\n\n.drop-nospace {\n  padding: 0;\n}\n\n// special\n\n.drop-disable {\n  // disable custom animation\n  > .drop > .drop-inner {\n    > .drop-content {\n      opacity: 1 !important;\n      .trans-anim-none() !important;\n    }\n  }\n}\n\n.drop-overflow-y {\n  > .drop-inner > .drop-content {\n    overflow-y: auto;\n    max-height: 25rem;\n    .overflow-style(@mode: inner);\n  }\n}\n\n.drop-overflow-x {\n  > .drop-inner > .drop-content {\n    overflow-x: auto;\n    max-width: 25rem;\n    .overflow-style(@mode: inner);\n  }\n}\n\n// animation\n\n.drop-container {\n  > .drop {\n    > .drop-inner {\n      > .drop-design {\n        opacity: 0;\n      }\n      > .drop-content {\n        opacity: 0;\n      }\n    }\n  }\n  &.in {\n    animation-duration: @time-medium + @time-tiny; // needed for animation time and backdrop animation time, put max animation time\n    > .drop {\n      > .drop-inner {\n        > .drop-design {\n          opacity: 1;\n          transition: opacity @time-medium @ease-in;\n        }\n        > .drop-content {\n          opacity: 1;\n          transition: opacity @time-medium @ease-in @time-tiny;\n        }\n      }\n    }\n  }\n  &.out {\n    animation-duration: @time-small + @time-tiny; // needed for animation time and backdrop animation time, put max animation time\n    > .drop {\n      > .drop-inner {\n        > .drop-design {\n          opacity: 0;\n          transition: opacity @time-small @ease-out @time-tiny;\n        }\n        > .drop-content {\n          opacity: 0;\n          transition: opacity @time-small @ease-out;\n        }\n      }\n    }\n  }\n}\n"