export default "@import 'xtend-library/src/core/link/_link.less';\n\n/**\n * decoration\n */\n\n.link-underline() {\n  text-decoration: underline;\n  &:hover, &.hover, &:active, &.active {\n    text-decoration: underline;\n  }\n}\n\n.link-none() {\n  text-decoration: none !important;\n  &:hover, &.hover, &:active, &.active {\n    text-decoration: none !important;\n  }\n}\n\n/**\n * link\n */\n\na, .btn-link {\n  .link-underline();\n}\n\n// animation\n\na, .btn-link {\n  transition: color @time-small @ease-out;\n  &:hover, &.hover, &:active, &.active {\n    transition: color @time-small @ease-in;\n  }\n}\n\n// text\n\n.text-default {\n  // link color\n  a:not(.btn), .btn-link {\n    color: @accent;\n    &:hover, &.hover, &:active, &.active {\n      color: @accent-dark;\n    }\n  }\n}\n\n.text-inverse {\n  // link color\n  a:not(.btn), .btn-link {\n    color: @text-inverse;\n    &:hover, &.hover, &:active, &.active {\n      color: @text-inverse-dark;\n    }\n  }\n}\n"