export default "/**\n * overlay-message-outside\n */\n\n.overlay-message-outside {\n  position: fixed;\n  z-index: @index-overlay !important;\n  top: 50%;\n  width: calc(100vw - 510px);\n  text-align: center;\n  @media @max-sm {\n    display: none;\n  }\n  .overlay-position-left & {\n    right: 0;\n  }\n  .overlay-position-right & {\n    left: 0;\n  }\n  // animation\n  opacity: 0;\n  .overlay-position-left.in & {\n    .anim-x-inverse.in();\n  }\n  .overlay-position-left.out & {\n    .anim-x.out();\n  }\n  .overlay-position-right.in & {\n    .anim-x.in();\n  }\n  .overlay-position-right.out & {\n    .anim-x-inverse.out();\n  }\n}\n"