export default "/**\n * drop\n */\n\n.drop-container {\n  display: inline-flex;\n  vertical-align: middle;\n  position: relative;\n  // z-index\n  > .drop {\n    z-index: inherit;\n    &.in {\n      z-index: @index-drop;\n    }\n    &.out {\n      z-index: @index-drop - 1;\n    }\n  }\n  // content\n  > .backdrop, > .drop {\n    display: none;\n  }\n  &.active, &.out {\n    > .backdrop, > .drop {\n      display: block;\n    }\n  }\n}\n\n.drop {\n  position: absolute;\n  animation-duration: inherit; // needed for animation time and backdrop animation time, put max animation time\n  > .drop-inner {\n    position: relative;\n    z-index: @index-backdrop + 1;\n    > .drop-design {\n      .design-setup();\n      border-radius: inherit;\n    }\n    > .drop-design, > .drop-content {\n      border-radius: inherit;\n    }\n  }\n}\n\n// special\n\n.drop-container-static {\n  position: static;\n  > .drop {\n    width: 100%;\n    > .drop-inner {\n      width: 100%;\n      > .drop-content {\n        max-width: none;\n      }\n    }\n    // @FIX no horizontal drop position\n    left: 0 !important;\n    right: 0 !important;\n  }\n}\n\n// before + after\n\n.drop-before() {\n  padding: @drop-space;\n  left: auto;\n  right: 100%;\n}\n\n.drop.drop-before {\n  .drop-before() !important;\n}\n\n.drop-after() {\n  padding: @drop-space;\n  left: 100%;\n  right: auto;\n}\n\n.drop.drop-after {\n  .drop-after() !important;\n}\n\n// left + right\n\n.drop-left() {\n  padding: @drop-space 0;\n  left: 0;\n  right: auto;\n}\n\n.drop.drop-left {\n  .drop-left() !important;\n}\n\n.drop-right() {\n  padding: @drop-space 0;\n  left: auto;\n  right: 0;\n}\n\n.drop.drop-right {\n  .drop-right() !important;\n}\n\n// bottom + top\n\n.drop-bottom() {\n  padding: @drop-space 0;\n  top: 100%;\n  bottom: auto;\n}\n\n.drop.drop-bottom {\n  .drop-bottom() !important;\n}\n\n.drop-top() {\n  padding: @drop-space 0;\n  top: auto;\n  bottom: 100%;\n}\n\n.drop.drop-top {\n  .drop-top() !important;\n}\n\n// center\n\n.drop-center() {\n  &:before {\n    display: none;\n    content: 'xt-drop-center';\n  }\n}\n\n.drop.drop-center {\n  .drop-center() !important;\n}\n\n// middle\n\n.drop-middle() {\n  padding-top: 0;\n  padding-bottom: 0;\n  &:after {\n    display: none;\n    content: 'xt-drop-middle';\n  }\n}\n\n.drop.drop-middle {\n  .drop-middle() !important;\n}\n\n// contain\n\n.drop-contain-bottom() {\n  .drop-middle();\n  top: auto;\n  bottom: 0;\n}\n\n.drop.drop-contain-bottom {\n  .drop-contain-bottom() !important;\n}\n\n.drop-contain-top() {\n  .drop-middle();\n  top: 0;\n  bottom: auto;\n}\n\n.drop.drop-contain-top {\n  .drop-contain-top() !important;\n}\n\n// .drop-disable\n\n.drop-disable {\n  .xt-disable();\n  position: static;\n  z-index: initial;\n  top: auto;\n  left: auto;\n  bottom: auto;\n  right: auto;\n  > .btn {\n    display: none !important;\n  }\n  > .drop {\n    display: block !important;\n    position: relative;\n    > .drop-inner {\n      z-index: initial;\n      min-width: auto;\n      > .drop-design {\n        display: none;\n      }\n      > .drop-content {\n        max-width: none;\n        max-height: none;\n      }\n    }\n  }\n}\n"