export default "/**\n * group-number\n */\n\n .group-number {\n  // group syule\n  .group-default();\n  // disable browser styles\n  input[type=\"number\"] {\n    -moz-appearance: textfield;\n    &::-webkit-inner-spin-button, &::-webkit-outer-spin-button {\n      -webkit-appearance: none;\n      appearance: none;\n    }\n  }\n}\n\n.group-number-line {\n  .form-item {\n    width: 5.5rem;\n  }\n}\n\n.group-number-stack {\n  .form-item {\n    width: 5.5rem;\n  }\n}\n"