export default "@import 'xtend-library/src/extensions/loader/loader.less';\n\n#iframe--infinite-scroll {\n//##START\n/**\n  * .block-listing\n  */\n\n.block-listing {\n}\n\n.block-listing_inner {\n  > .row {\n    .row-space(small);\n    .row-space(medium, sm);\n  }\n}\n\n.block-listing_col {\n  .col(12);\n  .col(6, sm);\n  .col(4, lg);\n}\n\n.block-listing_col--big {\n  .col(12, sm);\n  .col(12, lg);\n}\n\n.block-listing_item {\n  .link-none();\n  .full();\n  .text-default() !important;\n}\n\n.block-listing_img {\n  .media-container {\n    .padding(bottom, 75%);\n    background: @shade-200;\n  }\n}\n\n.block-listing_col--big {\n  .block-listing_img {\n    .media-container {\n      @media @min-sm {\n        padding-bottom: 37.5%;\n      }\n    }\n  }\n}\n\n.block-listing_content {\n  .padding(y, medium);\n}\n\n.block-listing_title {\n  .spacing-medium();\n  .font-sans-semibold();\n  .line-height-small();\n  font-size: 1.8rem;\n  letter-spacing: 0;\n  text-transform: none;\n}\n\n.block-listing_price {\n  .font-sans-semibold();\n  .line-height-small();\n  font-size: 1.4rem;\n  letter-spacing: 0.1em;\n  text-transform: none;\n}\n\n/**\n * .infinite-scroll\n */\n\n.infinite-scroll {\n  .margin(top, big);\n  > .list-block {\n    .list-space(tiny);\n    align-items: center;\n  }\n}\n\n.infinite-scroll-pre {\n  display: none;\n  .margin(bottom, big);\n  > .list-block {\n    .list-space(tiny);\n    align-items: center;\n  }\n}\n\n.infinite-scroll--more {\n  .font-sans-semibold();\n  font-size: 12px;\n  letter-spacing: .05em;\n}\n\n.infinite-scroll--spinner {\n  .loader-animated.out();\n}\n.infinite-scroll-loading {\n  .infinite-scroll--spinner {\n    .loader-animated.in();\n  }\n}\n\n[data-xt-infinitescroll-trigger] {\n  .content-nomore {\n    display: none;\n  }\n  .content {\n    transition: transform @time-small @ease-out-expo, opacity @time-small @ease-out-expo;\n  }\n}\n\n.infinite-scroll-loading {\n  [data-xt-infinitescroll-trigger] {\n    pointer-events: none;\n    > .content {\n      opacity: 0;\n      transform: translateY(1rem);\n      transition: transform @time-small @ease-out-expo, opacity @time-small @ease-out-expo;\n    }\n  }\n}\n\n.infinite-scroll-nomore {\n  [data-xt-infinitescroll-trigger] {\n    .disabled();\n    pointer-events: none;\n    .content {\n      display: none;\n    }\n    .content-nomore {\n      display: block;\n    }\n  }\n}\n\n.infinite-scroll-reset {\n  .infinite-scroll-pre {\n    display: block;\n  }\n}\n//##END\n}\n"