export default "/**\n * slider\n */\n\n// slider\n\n.slider {\n  position: relative;\n  overflow: hidden;\n}\n\n.slides {\n  position: relative;\n  // @FIX autoheight values\n  display: inline-block;\n  // @FIX inline-block margin below\n  vertical-align:top;\n}\n\n// slides\n\n.slide {\n  position: relative;\n  > * {\n    display: block;\n    position: relative;\n  }\n}\n\n// .slider-nooverflow\n\n.slider-nooverflow {\n  .xt-disable();\n  .slider-pagination, [data-xt-nav], .xt-wrap {\n    display: none !important;\n  }\n  // @FIX disable slider if not overflowing\n  .slides-inner {\n    .transform-none() !important;\n    .transition-none() !important;\n  }\n}\n\n// .slider-disable\n\n.slider-disable {\n  .xt-disable();\n  .slider-pagination, [data-xt-nav] {\n    display: none !important;\n  }\n  .xt-wrap {\n    visibility: hidden !important;\n    opacity: 0 !important;\n  }\n  // @FIX do calculation first\n  &.xt-disabled {\n    .slide:not(.active) {\n      visibility: hidden !important;\n      opacity: 0 !important;\n      height: 0 !important;\n    }\n  }\n}\n\n// .slider-expand\n\n.slider-expand {\n  .xt-disable();\n  .slides {\n    overflow: visible;\n    height: auto !important;\n  }\n  .slides-inner {\n    .trans-anim-none() !important;\n    .transform-none() !important;\n  }\n  .slides-inner {\n    flex-wrap: wrap !important;\n  }\n  .slide:not(.active) {\n    opacity: 1 !important;\n  }\n  .xt-wrap, .slider-pagination, [data-xt-nav], .loader, .filler, .spinner {\n    display: none !important;\n  }\n}\n"