export default "/**\n * backdrop\n */\n\n.backdrop {\n  position: fixed;\n  z-index: @index-backdrop;\n  top: 0;\n  right: 0;\n  width: 100%;\n  height: 100%;\n}\n\n/**\n * xt-\n */\n\n.xt-pointer-events-none {\n  pointer-events: none;\n}\n\n.xt-links-none {\n  a&, a {\n    pointer-events: none;\n  }\n}\n\n.xt-jump {\n  &:not(.active) {\n    cursor: pointer;\n  }\n  &.xt-jumps-none, .xt-jumps-none & {\n    cursor: inherit;\n  }\n}\n\n.xt-grab {\n  .user-select-none();\n  cursor: grab;\n  a, img {\n    // prevent browser drag\n    -webkit-user-drag: none;\n    user-drag: none;\n    // prevent browser selection\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n  }\n}\n\n.xt-disable-noinit {\n  &:after {\n    display: none;\n    content: 'xt-disable-noinit';\n  }\n}\n\n.xt-disable {\n  &:after {\n    display: none;\n    content: 'xt-disable';\n  }\n}\n\n.xt-hide {\n  position: absolute !important;\n  visibility: hidden !important;\n  opacity: 0 !important;\n}\n\n.xt-show {\n  position: relative !important;\n  visibility: visible !important;\n  opacity: 1 !important;\n}\n\n.xt-calculating {\n  display: block !important;\n  visibility: hidden !important;\n  opacity: 0 !important;\n  .trans-anim-none() !important;\n}\n\n.xt-container {\n  position: relative;\n  width: 100%;\n}\n\nhtml.xt-scrollbar {\n  body {\n    overflow-y: hidden !important;\n  }\n  .overlay {\n    overflow-y: scroll !important;\n  }\n}\n\n/**\n * pag nav\n */\n\n[data-xt-pag].xt-ignore {\n  display: none !important;\n}\n\n[data-xt-nav] {\n  cursor: pointer;\n}\n\n/**\n * xt-indicator\n */\n\n.xt-indicator {\n  pointer-events: none;\n  position: fixed;\n  right: 0;\n  margin-top: -9px;\n  width: 100px;\n  height: 18px;\n  z-index: @index-indicator;\n  line-height: 0;\n  text-align: right;\n  &:before {\n    display: inline-block;\n    position: relative;\n    padding: 3px 6px;\n    font-size: 12px;\n    line-height: 1;\n    text-transform: uppercase;\n  }\n}\n\n.xt-indicator-trigger {\n  right: 100px;\n  background-color: purple;\n  &:before {\n    content: 'trigger';\n    color: white;\n    background-color: purple;\n  }\n}\n\n.xt-indicator-start {\n  background-color: red;\n  &:before {\n    content: 'start';\n    color: white;\n    background-color: red;\n  }\n}\n\n.xt-indicator-end {\n  background-color: blue;\n  &:before {\n    content: 'end';\n    color: white;\n    background-color: blue;\n  }\n}\n\n.xt-indicator-start-real {\n  background-color: red;\n  opacity: .3;\n  z-index: @index-indicator - 1;\n  &:before {\n    content: 'start real';\n    color: white;\n    background-color: red;\n  }\n}\n\n.xt-indicator-end-real {\n  background-color: blue;\n  opacity: .3;\n  z-index: @index-indicator - 1;\n  &:before {\n    content: 'end real';\n    color: white;\n    background-color: blue;\n  }\n}\n"