export default "import { Xt } from 'xtend-library/src/xt.js'\n\nXt.mount.push({\n  matches: '.demo--toggle-timing-delay-fnc',\n  mount: object => {\n    // init\n\n    let self = new Xt.Toggle(object, {\n      delayOn: current => {\n        return Math.min(current * 150, 300)\n      },\n      delayOff: (current, total) => {\n        return Math.min((total - current) * 150, 300)\n      },\n    })\n\n    // unmount\n\n    const unmount = () => {\n      self.destroy()\n      self = null\n    }\n    return unmount\n  },\n})\n\nXt.mount.push({\n  matches: '.demo--toggle-timing-delay-fnc--hover',\n  mount: object => {\n    // init\n\n    let self = new Xt.Toggle(object, {\n      on: 'mouseenter',\n      off: 'mouseleave',\n      delayOn: current => {\n        return Math.min(current * 150, 300)\n      },\n      delayOff: (current, total) => {\n        return Math.min((total - current) * 150, 300)\n      },\n    })\n\n    // unmount\n\n    const unmount = () => {\n      self.destroy()\n      self = null\n    }\n    return unmount\n  },\n})\n"