export default "@import 'xtend-library/src/core/card/_card.less';\n\n/**\n * vars\n */\n\n// generate class: [false], [true] non responsive only, [sm, md, lg] responsive @breakpoints\n\n@generate-card-group: sm, md, lg;\n@generate-box-horizontal: sm, md, lg;\n\n/**\n * card\n */\n\n.card {\n  border-radius: 6px;\n  > .card-design {\n    border-width: 1px;\n    border-style: solid;\n    border-color: transparent;\n  }\n  // default options\n  .card-medium();\n}\n\n// custom blocks\n\n.card-block-intro {\n  background: fade(@black, 10%);\n}\n\n.card-block-side {\n  background: fade(@black, 10%);\n}\n\n// preset\n\n.card-drop {\n  // default options\n  width: 100vw; // fill size max-width\n}\n\n.card-overlay {\n  // default options\n  > .btn-close {\n    .btn-big();\n    .btn-icon();\n    top: 2rem;\n    right: 2rem;\n  }\n}\n\n.card-slide {\n  // default options\n  color: @text;\n  > .card-design {\n    border-color: @shade-200;\n    background: @shade-200;\n  }\n  > .card-inner > .card-content {\n    justify-content: center;\n    align-items: center;\n  }\n}\n\n// variant\n\n.card-default {\n  .text-default();\n  > .card-design {\n    border-color: @white;\n    background: @white;\n  }\n}\n\n.card-primary {\n  .text-inverse();\n  > .card-design {\n    border-color: @accent;\n    background: @accent;\n  }\n}\n\n// size\n\n.card-small {\n  .card-block {\n    .font-small();\n    .line-height-medium();\n    padding: 2em 2.5em;\n    .card-title {\n      .h4();\n      .spacing-small();\n    }\n  }\n}\n\n.card-medium {\n  .card-block {\n    .font-medium();\n    .line-height-medium();\n    padding: 2em 2.5em;\n    .card-title {\n      .h3();\n      .spacing-medium();\n    }\n  }\n}\n\n.card-large {\n  .card-block {\n    .font-big();\n    .line-height-medium();\n    padding: 2em 2.5em;\n    .card-title {\n      .h2();\n      .spacing-medium();\n    }\n  }\n}\n\n// special\n\n.card-nodesign {\n  > .card-design {\n    display: none;\n  }\n}\n\n.card-squared {\n  border-radius: 0;\n}\n\n.card-noborder {\n  > .card-design {\n    border: 0;\n  }\n}\n\n// special\n\n.card-overflow-y {\n  overflow-x: hidden;\n  overflow-y: auto;\n  max-height: 30rem;\n  .overflow-style(@mode: inner);\n}\n\n.card-disable {\n  color: @text !important;\n  &, .card-item {\n    opacity: 1 !important;\n    .transform-none() !important;\n    .trans-anim-none() !important;\n  }\n}\n"