export default "/**\n * gatsby_logo-icon\n */\n\n.gatsby_logo-icon {\n  .link-none();\n  display: inline-flex;\n  align-items: center;\n  height: 100%;\n  img {\n    width: 2rem;\n    height: 1.4rem;\n    .gatsby_site_header & {\n      width: 8rem;\n      height: 2.4rem;\n    }\n  }\n  // animation\n  img {\n    transition: opacity @time-tiny @ease-out;\n  }\n  &:hover, &.hover, &:active {\n    img {\n      opacity: 0.9;\n    }\n  }\n}\n\n/**\n * .gatsby_make-line\n */\n\n.gatsby_make-line {\n  position: relative;\n  z-index: 1;\n  .gatsby_make-line_container {\n    display: block;\n    position: relative;\n    width: 100%;\n    .gatsby_make-line_line {\n      .link-none();\n      display: inline-block;\n      padding-right: 15px;\n      background: @white;\n    }\n    .gatsby_make-line_line:after {\n      content: '';\n      position: absolute;\n      z-index: -1;\n      bottom: .5em;\n      left: 0;\n      width: 100%;\n      height: 2px;\n      background: @shade-200-dark;\n    }\n  }\n  // animation\n  .gatsby_make-line_container {\n    .gatsby_make-line_line:after {\n      left: 100%;\n      width: 0;\n      opacity: 0;\n      transition: all @time-tiny @ease-out initial;\n    }\n  }\n  &:hover, &.hover, &:active, &.active {\n    .gatsby_make-line_container {\n      .gatsby_make-line_line:after {\n        left: 0;\n        width: 100%;\n        opacity: 1;\n        transition: all @time-tiny @ease-in @time-tiny;\n      }\n    }\n  }\n}\n\n/**\n * .gatsby_make-anchor\n */\n\n.gatsby_make-anchor {\n  position: relative;\n  width: 100%;\n  a {\n    display: block;\n    .link-none();\n    color: @text !important;\n  }\n  .gatsby_site_article_anchor {\n    pointer-events: none;\n  }\n  // animation\n  .gatsby_site_article_anchor {\n    opacity: 0;\n    transition: all @time-small @ease-out, @time-tiny;\n  }\n  &:hover, &.hover, &:active, &.active {\n    .gatsby_site_article_anchor {\n      opacity: 1;\n      transition: all @time-small @ease-in initial;\n    }\n  }\n}\n\n.gatsby_site_article_anchor {\n  position: absolute;\n  z-index: 2;\n  bottom: 0;\n  right: 12px;\n  h2 & {\n    bottom: 3px;\n  }\n  .btn {\n    .link-none();\n    .btn-squared();\n    .btn-nodesign();\n    padding: 7px;\n    width: 30px;\n    height: 30px;\n    border-radius: 50%;\n    border-color: @white !important;\n    background: @white !important;\n    font-size: 17px;\n    color: @accent;\n    line-height: 1;\n  }\n}\n"