export default "import { Xt } from 'xtend-library/src/xt.js'\nimport 'xtend-library/src/core/toggle/toggle.js'\nimport RJSON from 'relaxed-json'\n\n/**\n * Smooth\n */\nclass Smooth extends Xt.Toggle {\n  /**\n   * constructor\n   * @param {Node|HTMLElement|EventTarget|Window} object Base node\n   * @param {Object} optionsCustom User options\n   * @constructor\n   */\n  constructor(object, optionsCustom = {}) {\n    super(object, optionsCustom)\n  }\n}\n\n//\n// options\n//\n\nSmooth.componentName = 'xt-smooth'\nSmooth.optionsDefault = {\n  class: false,\n  on: false,\n  wheel: {\n    selector: 'scrollingElement',\n    block: false,\n    limit: true,\n    transform: false,\n    horizontal: false,\n    factor: 1,\n    friction: delta => {\n      return delta / 11\n    },\n    frictionLimit: 1.5,\n  },\n}\n\n//\n// export\n//\n\nXt.Smooth = Smooth\n\n//\n// observe\n//\n\nXt.mount.push({\n  matches: '[data-' + Xt.Smooth.componentName + ']',\n  mount: object => {\n    // vars\n\n    const optionsMarkup = object.getAttribute('data-' + Xt.Smooth.componentName)\n    const options = optionsMarkup ? RJSON.parse(optionsMarkup) : {}\n\n    // init\n\n    let self = new Xt.Smooth(object, options)\n\n    // unmount\n\n    const unmount = () => {\n      self.destroy()\n      self = null\n    }\n    return unmount\n  },\n})\n"