export default "@import 'xtend-library/src/core/overlay/_overlay.less';\n@import 'xtend-library/src/core/javascript/javascript.less';\n@import 'xtend-library/src/core/card/card.less';\n\n/**\n * overlay\n */\n\n.overlay {\n  .overlay-inner {\n    border-radius: 6px;\n  }\n  .overlay-design {\n    border-width: 1px;\n    border-style: solid;\n    border-color: transparent;\n  }\n  .overlay-container {\n    .overlay-space();\n  }\n  // default options\n  .overlay-size-small();\n  .overlay-medium();\n}\n\n// space\n\n.overlay-space {\n  // same as .container-space\n  padding: 1.5rem;\n  @media @min-xxs {\n    padding: 2rem;\n  }\n  @media @min-xs {\n    padding: 3rem;\n  }\n  @media @min-sm {\n    padding: 3rem;\n  }\n  @media @min-md {\n    padding: 4rem;\n  }\n  @media @min-lg {\n    padding: 5rem;\n  }\n  @media @min-xl {\n    padding: 6rem;\n  }\n}\n\n// variant\n\n.overlay-default {\n  .overlay-inner {\n    .text-default();\n  }\n  .overlay-design {\n    border-color: @shade-200;\n    background: @white;\n    box-shadow: @shadow-light;\n  }\n}\n\n.overlay-primary {\n  .overlay-inner {\n    .text-inverse();\n  }\n  .overlay-design {\n    border-color: @accent-dark;\n    background: @accent;\n    box-shadow: @shadow-dark;\n  }\n}\n\n// size\n\n.overlay-size-tiny {\n  .overlay-inner, .card-overlay > .card-inner > .card-content {\n    max-width: 500px;\n  }\n}\n\n.overlay-size-small {\n  .overlay-inner, .card-overlay > .card-inner > .card-content {\n    max-width: 700px;\n  }\n}\n\n.overlay-size-medium {\n  .overlay-inner, .card-overlay > .card-inner > .card-content {\n    max-width: 900px;\n  }\n}\n\n.overlay-size-large {\n  .overlay-inner, .card-overlay > .card-inner > .card-content {\n    max-width: 1200px;\n  }\n}\n\n.overlay-size-big {\n  .overlay-inner, .card-overlay > .card-inner > .card-content {\n    max-width: 1600px;\n  }\n}\n\n.overlay-size-full {\n  .overlay-inner, .card-overlay > .card-inner > .card-content {\n    max-width: none;\n  }\n}\n\n// content size\n\n.overlay-small {\n  // card\n  .card-overlay {\n    .card-small();\n    .card-block {\n      padding: 2em 2em !important;\n    }\n  }\n}\n\n.overlay-medium {\n  // card\n  .card-overlay {\n    .card-small();\n    .card-block {\n      padding: 2em 2em !important;\n    }\n    @media @min-sm {\n      .card-medium();\n      .card-block {\n        padding: 2.5em 2.5em !important;\n      }\n    }\n  }\n}\n\n.overlay-large {\n  // card\n  .card-overlay {\n    .card-medium();\n    .card-block {\n      padding: 2.5em 2.5em !important;\n    }\n    @media @min-sm {\n      .card-large();\n      .card-block {\n        padding: 3em 3em !important;\n      }\n    }\n  }\n}\n\n// special\n\n.overlay-squared {\n  .overlay-design {\n    border-radius: 0;\n  }\n}\n\n.overlay-noborder {\n  .overlay-design {\n    border: 0;\n  }\n}\n\n.overlay-disable {\n  // disable animation\n  .card-overlay {\n    > .card-inner {\n      > .card-block,  > .card-content {\n        opacity: 1 !important;\n        .transform-none() !important;\n        .trans-anim-none() !important;\n      }\n    }\n  }\n}\n\n// animation\n\n.overlay {\n  .overlay-design {\n    opacity: 0;\n  }\n  .card-overlay {\n    > .btn-close {\n      opacity: 0;\n    }\n    > .card-inner {\n      > .card-block,  > .card-content {\n        opacity: 0;\n      }\n    }\n  }\n  &.in {\n    animation-duration: @time-medium + @time-tiny; // needed for animation time and backdrop animation time, put max animation time\n    .overlay-design {\n      opacity: 1;\n      transition: opacity @time-medium @ease-in;\n    }\n    .card-overlay {\n      > .btn-close {\n        // use animation to not override .btn transitions\n        .anim-zoom.in();\n        animation-delay: @time-medium;\n      }\n      > .card-inner {\n        > .card-block,  > .card-content {\n          opacity: 1;\n          transition: opacity @time-medium @ease-in @time-tiny;\n        }\n      }\n    }\n  }\n  &.out {\n    animation-duration: @time-small + @time-tiny; // needed for animation time and backdrop animation time, put max animation time\n    .overlay-design {\n      opacity: 0;\n      transition: opacity @time-small @ease-out @time-tiny;\n    }\n    .card-overlay {\n      > .btn-close {\n        // use animation to not override .btn transitions\n        .anim-zoom.out();\n        animation-delay: initial;\n      }\n      > .card-inner {\n        > .card-block,  > .card-content {\n          opacity: 0;\n          transition: opacity @time-small @ease-out;\n        }\n      }\n    }\n  }\n}\n"