export default ".demo--multilevel {\n  /**\n  * multilevel\n  */\n  .multilevel-title-wrapper {\n    .full();\n    .margin(y, layout) !important;\n    .margin(top, medium);\n    .margin(bottom, tiny);\n    position: relative;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n  }\n  .multilevel-list {\n    // btn\n    .list-btns({\n      .full();\n      .btn-text();\n      .btn-tall();\n      .btn-none-x();\n      margin-bottom: -1px !important;\n      border-top-color: @shade-200 !important;\n      border-bottom-color: @shade-200 !important;\n    });\n    // caret\n    > li {\n      > button {\n        .caret();\n        .caret.caret-horizontal();\n      }\n    }\n  }\n  .multilevel-reset {\n    .caret.caret-horizontal.caret-inverse();\n  }\n  .multilevel-tab {\n    .full();\n    .toggle-block();\n    // animation\n    .anim-x();\n    animation-duration: @time-tiny;\n    &.inverse {\n      .anim-x-inverse();\n      animation-duration: @time-tiny;\n    }\n  }\n}\n"