export default "/**\n * label\n */\n\n.form-label {\n  display: inline-block;\n  position: relative;\n  cursor: pointer;\n}\n\n/**\n * item\n */\n\n.form-item {\n  // blok fixes select and textarea space below\n  display: block;\n  .full();\n  &:focus, &.active {\n    z-index: @index-active;\n  }\n}\n\n/**\n * textarea\n */\n\n.form-item {\n  textarea& {\n    &.textarea-autosize {\n      overflow: hidden;\n      resize: none;\n    }\n  }\n}\n\n/**\n * select\n */\n\n.form-item {\n  select:not([multiple])& {\n    .padding(right, @select-space-size) !important;\n    white-space: normal;\n    background-position: calc(~'100% - ' @select-space-size / 2) 50%, calc(~'100% - (' @select-space-size / 2~') + '@select-arrow-size) 50%, 100% 0;\n    background-size: @select-arrow-size @select-arrow-size, @select-arrow-size @select-arrow-size, @select-space-size 100%;\n    background-repeat: no-repeat;\n  }\n}\n\n/**\n * check and radio\n */\n\n.checkbox, .radio {\n  position: relative !important;\n  &:not(.disabled) {\n    cursor: pointer;\n  }\n  // check and radio styled design\n  &:before, &:after {\n    content: '';\n    position: absolute !important;\n    top: 0;\n    left: 0;\n  }\n  &:before {\n    pointer-events: none;\n  }\n  &:after {\n    display: flex !important;\n    align-items: center;\n    justify-content: center;\n  }\n}\n\n// styled\n\ninput[type=\"checkbox\"]:not(.unstyled), input[type=\"radio\"]:not(.unstyled) {\n  position: absolute !important;\n  z-index: -1 !important;\n  opacity: 0 !important;\n}\n\ninput[type=\"checkbox\"]:not(.unstyled) {\n  & ~ .form-label {\n    .checkbox();\n  }\n}\n\ninput[type=\"radio\"]:not(.unstyled) {\n  & ~ .form-label {\n    .radio();\n  }\n}\n\n// pseudo\n\ninput[type=\"checkbox\"]:not(.unstyled) {\n  // focus\n  &:focus ~ .form-label {\n    .checkbox.focus();\n  }\n  // active\n  &:checked ~ .form-label {\n    .checkbox.active();\n  }\n  // disabled\n  &:disabled ~ .form-label {\n    .disabled();\n  }\n}\n\ninput[type=\"radio\"]:not(.unstyled) {\n  // focus\n  &:focus ~ .form-label {\n    .radio.focus();\n  }\n  // active\n  &:checked ~ .form-label {\n    .radio.active();\n  }\n  // disabled\n  &:disabled ~ .form-label {\n    .disabled();\n  }\n}\n\n// unstyled\n\ninput[type=\"checkbox\"].unstyled, input[type=\"radio\"].unstyled {\n  display: block;\n  float: left;\n  & ~ .form-label {\n    display: block;\n    overflow: hidden;\n  }\n  & ~ .form-label {\n    .checkradio-label-unstyled() !important;\n  }\n}\n"